<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- content -->
            <div>
              <!-- header -->
              <div class="row mt-4 mb-4">
                <div class="col-6 content-header">
                  <div class="pl-4">
                    <i class="far fa-list-alt"></i>
                    <h5 class="pl-2 d-inline-block">
                      <strong>PENDING ORDERS</strong>
                    </h5>
                  </div>
                </div>
                <div class="col-6"></div>
              </div>
              <hr class="pl-4 pr-4" />
              <div class="table-responsive table-scroll mt-4">
                <table
                  class="table table-hover table-sm table-borderless table-content text-center"
                >
                  <thead>
                    <tr>
                      <th>Sr.No.</th>
                      <th>Image</th>
                      <th>Item</th>
                      <th>Quantity</th>
                      <th>Ordered Quantity</th>
                    </tr>
                  </thead>
                  <tbody v-if="items.length > 0">
                    <tr v-for="(item, index) in items" :key="index+1">
                      <td>{{index+1}}</td>
                      <td>
                        <img :src="item.image" width="100" height="100" />
                      </td>
                      <td>{{item.name}}</td>
                      <td>{{item.qty}} {{item.unit}} * {{item.pivot.quantity}}</td>
                      <td>{{item.pivot.quantity}}</td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="4" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                            alt="Card image cap"
                            style="width:20em"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! There ain't any pending order</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <OrderFooter />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Api from "@/services/Api.js";
import Vue from "vue";
import OrderFooter from "../Order/OrderFooter.vue";

export default {
  name: "PendingOrderItems",
  components: {
    OrderFooter
  },

  data() {
    return {
      items: []
    };
  },

  mounted() {
    this.disable = true;
    setTimeout(() => {
      if (this.user) {
        this.fetchOrders();
      }
    }, 3000);
  },

  methods: {
    fetchOrders() {
      this.items = [];
      this.disable = true;
      Api()
        .get("collaborator/orders", {
          params: {
            status: 4
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            {
              let item = {
                image: doc.image,
                name: doc.name,
                qty: doc.qty,
                unit: doc.unit,
                pivot: doc.pivot
              };
              this.items.push(item);
            }
          });

          store.dispatch("patchorderPageCount", res.data.last_page);
          store.dispatch("patchorderPageNumber", res.data.current_page);
          store.dispatch("patchtotalOrders", this.items.length);
          this.disable = false;
        })
        .catch(err => {
          this.disable = false;
        });
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



