<template>
  <div>
    <div class="row">
      <div class="col-6 p-4"></div>
      <div class="col-6 p-4">
        <router-link
          to="/order/place"
          class="btn btn-success btn-sm float-right"
          active-class="active"
          tag="button"
          v-if="getUserPermission(user.permissions.salesOrder, 'post') || getUserPermission(user.permissions.customer, 'get')"
        >
          <span>+ Place Order</span>
        </router-link>
      </div>
    </div>

    <!-- cards -->
    <div class="row card-deck dashboard-cards pb-2">
      <div
        class="col-lg-3 card content border-1 shadow-sm"
        v-if="getUserPermission(user.permissions.salesOrder, 'get')"
      >
        <div class="card-body text-center">
          <div class="media">
            <div class="shopping-cart">
              <img src="@/assets/shopping-cart.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Total Orders</span>
              <h4>{{ totalOrders }}</h4>
            </div>
          </div>
        </div>
      </div>

      <div
        class="col-lg-3 card content border-1 shadow-sm"
        v-if="getUserPermission(user.permissions.salesOrder, 'get')"
      >
        <div class="card-body text-center">
          <div class="media">
            <div class="revenue">
              <img src="@/assets/ruppee.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Total Revenue</span>
              <h4>{{totalRevenue}}</h4>
            </div>
          </div>
        </div>
      </div>

      <div
        class="col-lg-3 card content border-1 shadow-sm"
        v-if="getUserPermission(user.permissions.customer, 'get')"
      >
        <div class="card-body text-center">
          <div class="media">
            <div class="user">
              <img src="@/assets/user.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Total App User</span>
              <h4>{{ users }}</h4>
            </div>
          </div>
        </div>
      </div>

      <div
        class="col-lg-3 card content border-1 shadow-sm"
        v-if="getUserPermission(user.permissions.salesOrder, 'get')"
      >
        <div class="card-body text-center">
          <div class="media">
            <div class="income">
              <img src="@/assets/coins.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Total Income</span>
              <h4>15,009</h4>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row pt-4 pb-2" v-if="getUserPermission(user.permissions.salesOrder, 'get')">
      <div class="col-lg-6">
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="media pb-2">
              <img
                src="@/assets/exclamation-mark.svg"
                class="img-fluid align-self-center exclamation-mark mr-2"
              />
              <div class="media body">
                <h5 class="font-weight-medium pt-2">Recent Pending Orders</h5>
              </div>
            </div>
            <div class="table-responsive dash-table table-scroll">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead class="small">
                  <tr>
                    <th>Order Id</th>
                    <th>Products</th>
                    <th>Items Ordered</th>
                    <th>Date</th>
                    <th>Amount</th>
                    <th>Ordered From</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody v-if="pendingOrders.length > 0">
                  <tr v-for="(order, index) in pendingOrders" :key="order.id">
                    <td>
                      {{ order.orderId.substring(0, 4) + '...' }}
                      <i
                        class="far fa-copy pl-1"
                        style="cursor:pointer"
                        @click="copyProductId(order.orderId)"
                      ></i>
                      <input type="hidden" :value="order.productId" :id="order.productId" />
                    </td>
                    <td>
                      <div v-if="order.products.length > 2">
                        <span
                          v-for="product in order.products.slice(0, 2)"
                          :key="product.productId"
                        >
                          <img :src="product.image" class="img-fluid content-image" />
                          +
                        </span>
                        <span>
                          <b>{{ order.products.length - 2 }} more</b>
                        </span>
                      </div>
                      <div v-else>
                        <span v-for="product in order.products" :key="product.productId">
                          <img :src="product.image" class="img-fluid content-image" />
                        </span>
                      </div>
                    </td>
                    <td>{{ order.orderedItemsCount }}</td>
                    <td>
                      <span class="text-dark">{{ order.orderDate }}</span>
                      <br />
                      <small>{{ order.orderTime }}</small>
                    </td>
                    <td class="text-dark">₹{{ order.payment }}</td>
                    <td>
                      <span v-if="order.placedFrom == 1">App</span>
                      <span v-if="order.placedFrom == 2">WhatsApp</span>
                      <span v-if="order.placedFrom == 3">Call</span>
                      <span v-if="order.placedFrom == 4">SMS</span>
                    </td>
                    <td class="text-dark">
                      <div>
                        <router-link
                          :to="{
                            name: 'ViewOrder',
                            params: { orderId: order.orderId }
                          }"
                        >
                          <i class="fa fa-eye"></i>
                        </router-link>
                        <router-link
                          :to="{
                            name: 'OrderEdit',
                            params: { orderId: order.orderId }
                          }"
                        >
                          <i class="fa fa-pencil-alt pl-2"></i>
                        </router-link>
                      </div>
                      <div>
                        <router-link
                          :to="{
                            name: 'OrderBill',
                            params: { slipType: 'bill', orderId: order.orderId }
                          }"
                          tag="a"
                          target="_blank"
                        >Generate Bill</router-link>
                      </div>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr>
                    <td colspan="7" class="text-center">
                      <div>
                        <img
                          class="card-img-top img-fluid"
                          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                          alt="Card image cap"
                          style="width:20em"
                        />
                        <div class="card-body">
                          <p class="card-text">Oops! There ain't any orders here</p>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="text-center mt-2">
              <router-link to="/order/pending" class="see-all" tag="a" target="_blank">See All</router-link>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-6">
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="media pb-2">
              <img
                src="@/assets/exclamation-mark.svg"
                class="img-fluid align-self-center exclamation-mark mr-2"
              />
              <div class="media body">
                <h5 class="font-weight-medium pt-2">Reminders</h5>
              </div>
            </div>
            <div class="table-responsive">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead class="small">
                  <tr>
                    <th>Number</th>
                    <th>Description</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{{ reminder.pendingOrders }}</td>
                    <td>Pending Orders</td>
                  </tr>
                  <tr>
                    <td>{{ reminder.urgentOrders }}</td>
                    <td>Urgent Orders To Complete</td>
                  </tr>
                  <tr>
                    <td>{{ reminder.outOfStock }}</td>
                    <td>Out of Stock</td>
                  </tr>
                  <tr>
                    <td>{{ reminder.lowStock }}</td>
                    <td>Low Stock</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="card content border-1 shadow-sm mt-2">
          <div class="card-body">
            <div class="media pb-2">
              <img
                src="@/assets/dcash.svg"
                class="img-fluid align-self-center exclamation-mark mr-2"
              />
              <div class="media body">
                <h5 class="font-weight-medium pt-2">Recently Added Products</h5>
              </div>
            </div>
            <div class="table-responsive dash-table table-scroll">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead class="small">
                  <tr>
                    <th>Product Id</th>
                    <th>Image</th>
                    <th>Name</th>
                    <th>Stock</th>
                    <th>currentPrice</th>
                    <th>Date</th>
                  </tr>
                </thead>
                <tbody v-if="products.length > 0">
                  <tr v-for="(product, index) in products" :key="product.productId">
                    <td>
                      {{ product.productId.substring(0, 4) + '...' }}
                      <i
                        class="far fa-copy pl-1"
                        style="cursor:pointer"
                        @click="copyProductId(product.productId)"
                      ></i>
                      <input type="hidden" :value="product.productId" :id="product.productId" />
                    </td>
                    <td>
                      <div v-if="product.image">
                        <img :src="product.image" class="img-fluid content-image" />
                      </div>
                    </td>
                    <td>{{ product.name }}</td>
                    <td>{{ product.stock }}</td>
                    <td>{{ product.currentPrice }}</td>
                    <td>{{ product.uploadedAt }}</td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr>
                    <td colspan="7" class="text-center">
                      <div>
                        <img
                          class="card-img-top img-fluid"
                          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                          alt="Card image cap"
                          style="width:20em"
                        />
                        <div class="card-body">
                          <p class="card-text">Oops! There ain't any products uploaded this week</p>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="text-center mt-2">
              <router-link to="/inventory/all" class="see-all" tag="a" target="_blank">See All</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { utcHours, utcMins } from "@/store/variable";
import Api from "@/services/Api.js";

export default {
  name: "Admin",

  data() {
    return {
      reminder: {
        pendingOrders: 0,
        urgentOrders: 0,
        outOfStock: 0,
        lowStock: 0
      },
      users: 0,
      products: [],
      pendingOrders: [],
      totalPay: 0,
      AmountPaid: 0,
      totalRevenue: 0,
      totalOrders: 0
    };
  },

  mounted() {
    setTimeout(() => {
      this.countTotalOrders();
      this.reminders();
      this.getPendingOrders();
      this.countTotalUsers();
      this.getProducts();
      this.getRevenue();
    }, 3000);
  },

  methods: {
    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },
    countTotalOrders() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalOrders = res.data;
            }
          });
      }
    },

    countTotalUsers() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        Api()
          .get("user", {
            params: {
              designation: "user",
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.users = res.data;
            }
          });
      }
    },

    reminders() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        let urgetOrders = 0;
        Api()
          .get("order", {
            params: {
              status: 4,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              Vue.set(this.reminder, "pendingOrders", res.data);
              urgetOrders = res.data;
            }
          });

        Api()
          .get("order", {
            params: {
              status: 5,
              compare: ["orderTime", "=", 1],
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              urgetOrders = urgetOrders + res.data;
              Api()
                .get("order", {
                  params: {
                    status: 5,
                    compare: ["orderTime", "=", 1],
                    count: "aajKuchTufaniKrteHai"
                  }
                })
                .then(res => {
                  if (res.data) {
                    urgetOrders = urgetOrders + res.data;
                    Vue.set(this.reminder, "urgetOrders", res.data);
                  }
                });
            }
          });

        //same day
        Api()
          .get("item", {
            params: {
              compare: ["stock", "<", 4],
              count: "laaiye"
            }
          })
          .then(res => {
            if (res.data) {
              Vue.set(this.reminder, "lowStock", res.data);
            }
          });

        //same day
        Api()
          .get("item", {
            params: {
              stock: 0,
              count: "laaiye"
            }
          })
          .then(res => {
            if (res.data) {
              Vue.set(this.reminder, "outOfStock", res.data);
            }
          });
      }
    },

    getPendingOrders() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        this.disable = true;
        Api()
          .get("order", {
            params: {
              status: 4,
              products: "budhbak",
              order: "updated_at",
              orderWay: "desc",
              page: 1,
              limit: 5
            }
          })
          .then(res => {
            let ordersList = [];
            res.data.data.forEach(doc => {
              let dateTime = new Date(doc.orderTime);
              dateTime.setHours(dateTime.getHours() + utcHours);
              dateTime.setMinutes(dateTime.getMinutes() + utcMins);
              let month = dateTime.getMonth() + 1;
              let date =
                dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
              var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
              var hours = dateTime.getHours() % 12 || 12;
              let time =
                hours +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds() +
                " " +
                AmOrPm;
              let order = {
                orderId: doc.id,
                products: doc.products,
                orderedItemsCount: doc.orderedItemsCount,
                orderDate: date,
                orderTime: time,
                payment: doc.totalPayableAmount,
                paidAmount: doc.paidAmount,
                placedFrom: doc.placedFrom
              };
              ordersList.push(order);
            });

            this.pendingOrders = ordersList;
          });
      }
    },

    getProducts() {
      if (
        this.getUserPermission(this.user.permissions.inventoryProducts, "get")
      ) {
        this.disable = true;
        Api()
          .get("item", {
            params: {
              compare: ["uploadedAt", "<=", 7],
              order: "uploadedAt",
              orderWay: "desc",
              page: 1,
              limit: 5
            }
          })
          .then(res => {
            let ordersList = [];
            res.data.data.forEach(doc => {
              let item = {
                productId: doc.productId,
                itemId: doc.itemId,
                name: doc.name,
                stock: doc.stock,
                unit: doc.unit,
                mrp: doc.MRP,
                currentPrice: doc.currentPrice,
                discount: doc.discount,
                image: doc.image,
                qty: doc.qty,
                uploadedAt: doc.uploadedAt
              };

              this.products.push(item);
            });
          });
      }
    },

    getRevenue() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 7,
              sum: "totalPayableAmount"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalRevenue = res.data;
            }
          });
      }
    }
  }
};
</script>

<style src="@/assets/css/dashBoards.css" scoped></style>
<style src="@/assets/css/tableContent.css" scoped></style>



