<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>Issues</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <!-- where is the user facing issue in -->
                <form class="form-inline float-right">
                  <select
                    v-model="issueIn"
                    name="status"
                    class="custom-select"
                    @change="fetchIssues(1)"
                  >
                    <option disabled="disabled">Issues In</option>
                    <option value="1" selected>App</option>
                    <option value="2">Webapp</option>
                    <option value="3">Website</option>
                  </select>
                </form>
              </div>
            </div>
            <!-- content divided into pending/resolved/abandoned -->
            <ul class="nav nav-tabs" id="issues" role="tablist">
              <li class="nav-item">
                <a
                  class="nav-link active"
                  id="home-tab"
                  @click="issueStatus('pending')"
                  data-toggle="tab"
                  href="#issues"
                  role="tab"
                  aria-controls="pending"
                  aria-selected="true"
                >Pending</a>
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  id="profile-tab"
                  @click="issueStatus('resolved')"
                  data-toggle="tab"
                  href="#issues"
                  role="tab"
                  aria-controls="resolved"
                  aria-selected="false"
                >Resolved</a>
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  id="contact-tab"
                  @click="issueStatus('abandoned')"
                  data-toggle="tab"
                  href="#issues"
                  role="tab"
                  aria-controls="abandoned"
                  aria-selected="false"
                >abandoned</a>
              </li>
            </ul>
            <div class="tab-content" id="issuesContent">
              <div
                class="tab-pane fade show active"
                id="issues"
                role="tabpanel"
                aria-labelledby="pending-tab"
              >
                <div class="table-responsive table-scroll mt-4">
                  <table
                    class="table table-hover table-sm table-borderless table-content text-left"
                  >
                    <thead>
                      <tr>
                        <th>Sr.No</th>
                        <th>Title</th>
                        <th>Issue</th>
                        <th>Priority</th>
                        <th>Image</th>
                        <th>Submitted By</th>
                        <th v-if="status=='resolved'">Resolved By</th>
                        <th v-if="status=='abandoned'">abandoned By</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody v-if="issues.length > 0">
                      <tr v-for="(issue, index) in issues" :key="index + 1">
                        <td>{{index+1}}</td>
                        <td>{{issue.title}}</td>
                        <td>{{issue.issue}}</td>
                        <td>{{issue.priority}}</td>
                        <td>
                          <img :src="issue.image" width="200" v-if="issue.image" />
                          <span v-else>N/A</span>
                        </td>
                        <td>{{issue.issue_by.name}}</td>
                        <td
                          v-if="status=='resolved' || status=='abandoned'"
                        >{{issue.solved_by.name}}</td>
                        <td>
                          <span v-if="issue.resolved">
                            <i class="fa fa-check text-success" aria-hidden="true"></i>
                          </span>
                          <span v-else-if="issue.abandoned">
                            <i class="fa fa-times text-danger" aria-hidden="true"></i>
                          </span>
                          <span v-else>
                            <i
                              class="fa fa-check pr-3"
                              aria-hidden="true"
                              @click.prevent="issueResolved(index)"
                            ></i>
                            <i
                              class="fa fa-times"
                              aria-hidden="true"
                              @click.prevent="issueAbandoned(index)"
                            ></i>
                          </span>
                        </td>
                      </tr>
                    </tbody>
                    <tbody v-else>
                      <tr>
                        <td colspan="9" class="text-center">
                          <div>
                            <img
                              class="card-img-top img-fluid"
                              src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                              alt="Card image cap"
                              style="width: 20em;"
                            />
                            <div class="card-body">
                              <p class="card-text">Hooray! No pending issue!</p>
                            </div>
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <issueFooter />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { bus } from "@/main";
import store from "@/store/store";
import issueFooter from "./footer";
import Api from "@/services/Api.js";

export default {
  name: "ViewPushNotification",
  components: {
    issueFooter
  },
  data() {
    return {
      issues: [],
      issueIn: 2,
      pendingIssues: [],
      resolvedIssues: [],
      abandonedIssues: [],
      status: "pending",
      disable: false,
      message: null,
      success: false
    };
  },

  mounted() {
    this.fetchIssues(1);
  },

  created() {
    bus.$on("paginateNotification", data => {
      this.fetchIssues(data["page"]);
    });
  },

  methods: {
    fetchIssues(page) {
      if (this.getUserPermission(this.user.permissions.issues, "get")) {
        this.issues = [];
        this.pendingIssues = [];
        this.resolvedIssues = [];
        this.abandonedIssues = [];
        if (!page) {
          page = 1;
        }
        this.disable = true;
        Api()
          .get("issue", {
            params: {
              in: this.issueIn,
              issueBy: "fetch",
              solvedBy: "fetch",
              order: "created_at",
              orderWay: "desc"
              //   page: page,
              //   limit: this.size
            }
          })
          .then(res => {
            let issuesList = [];
            res.data.forEach(doc => {
              let created_at = this.getDateTime(doc.created_at);
              if (doc.resolved) {
                this.resolvedIssues.push(doc);
              } else if (!doc.resolved && doc.abandoned) {
                this.abandonedIssues.push(doc);
              } else {
                this.pendingIssues.push(doc);
              }
              this.disable = false;
            });
            this.issues = this.pendingIssues;
          });
      }
    },

    issueStatus(status) {
      this.status = status;
      if (this.status == "resolved") {
        this.issues = this.resolvedIssues;
      } else if (this.status == "abandoned") {
        this.issues = this.abandonedIssues;
      } else {
        this.issues = this.pendingIssues;
      }
    },

    issueResolved(index) {
      if (this.getUserPermission(this.user.permissions.issues, "put")) {
        let url = "issue/" + this.issues[index].id;
        Api()
          .post(url, {
            _method: "PUT",
            resolved: "resolved"
          })
          .then(res => {
            // this.resolvedIssues.unshift(this.pendingIssues[index]);
            // this.issues.splice(index, 1);
            // this.pendingIssues.splice(index, 1);
            this.fetchIssues(1);
          });
      }
    },

    issueAbandoned(index) {
      if (this.getUserPermission(this.user.permissions.issues, "put")) {
        let url = "issue/" + this.issues[index].id;

        Api()
          .post(url, {
            _method: "PUT",
            abandoned: "abandoned"
          })
          .then(res => {
            // this.abandonedIssues.unshift(this.pendingIssues[index]);
            // this.issues.splice(index, 1);
            // this.pendingIssues.splice(index, 1);
            this.fetchIssues(1);
          });
      }
    }
  }
};
</script>



