<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";
import Dashboard from "./DashBoard.vue";
import Footer from "../Footer.vue";
import { bus } from "@/main";
import store from "@/store/store";
import { invLayoutMixin } from "@/components/Mixins/Inventory/invLayoutMixin.js";

export default {
  name: "inactiveItems",
  mixins: [invLayoutMixin],
  data() {
    return {
      items: [],
      loading: false
    };
  },

  components: {
    Footer,
    Dashboard
  },

  computed: {
    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = "item";
      if (!page) {
        page = 1;
      }

      if (this.user.designation == "webappUploader") {
        Api()
          .get(url, {
            params: {
              active: 0,
              user: "aajaBhai",
              category: "fetch",
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay,
              uploadedBy: firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              if (this.user.uuid == doc.uploadedBy) {
                let item = this.dataFormat(doc);
                this.items.push(item);
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      } else if (this.user.designation == "collaborator") {
        let cocats = this.user.collaboration[0].category_access;
        let total = 0;
        cocats.forEach(cocat => {
          Api()
            .get("item", {
              params: {
                active: 0,
                user: "aajaBhai",
                category: "fetch",
                itemId: cocat.item_id,
                order: this.orderItems,
                orderWay: this.orderWay
              }
            })
            .then(res => {
              console.log(total);
              console.log(res.data.length);
              total = total + res.data.length;
              res.data.forEach(doc => {
                {
                  let item = this.dataFormat(doc);
                  this.items.push(item);
                }
              });
              store.dispatch("patchinventoryPageCount", 1);
              store.dispatch("patchinventoryPageNumber", 1);
              store.dispatch("patchinventoryTotalItems", total);
              this.loading = false;
            })
            .catch(err => {
              this.loading = false;
            });
        });
      } else {
        Api()
          .get(url, {
            params: {
              active: 0,
              user: "aajaBhai",
              category: "fetch",
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              let item = this.dataFormat(doc);
              this.items.push(item);
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);

            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      }
    }
  }
};
</script>



