import Vue from "vue";
import Api from "@/services/Api.js";
import {
    inventoryDataMixin
} from './inventoryDataMixin.js';
import {
    imageControlMixin
} from '../imageControlMixin.js';
import $ from "jquery";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";

export const inventoryMixin = {
    mixins: [inventoryDataMixin, imageControlMixin],
    data() {
        return {
            active: "1",
            address: null,
            availableBrands: [],
            availableVendors: [],
            barcode: null,
            brand: null,
            disable: false,
            disPriority: 0,
            description: "<p></p>",
            dropShip: true,
            dropShipAddress: null,
            disVal: 0,
            detailImages: [],
            // ckeditor
            editor: ClassicEditor,
            editorData: "<p>Description</p>",
            editorConfig: {},

            enterVendor: false,
            enterBrand: false,
            expiryDate: null,
            errors: [],
            errorClass: 'small text-danger font-weight-bold',
            features: "<p></p>",
            ingredients: "<p></p>",
            image: null,
            itemId: null,
            maxLimit: 0,
            mfgDate: null,
            name: null,
            priceBand: 10,
            profitLoss: 0,
            profitLossPer: 0,
            searchingItem: null,
            searchProductID: null,
            shelfLife: null,
            sku: null,
            stock: 0,
            qty: 0,
            tags: null,
            unit: null,
            unit2: null,
            vendor: null,
            soldUnder: null,


            message: null,
            disableMessage: null,
            success: false,
            similarItems: [],

            duplicate: true,
            othersimilarProduct: false,
            mrp: 0,
            msp: 0,
            discount: 0,
            currentPrice: 0,
            similarProductId: null,

            units: [],
            category: null,
            subcategory: null,
            cocategory: null,
            categories: [],
            subcategories: [],
            cocategories: [],

            Discalculate: false, // disabling text field to calculate discount value
        }
    },

    created() {
        this.getBrands();
        this.getVendors();
        this.getUnits();
    },

    methods: {

        addBrand() {
            if (this.getUserPermission(
                    this.user.permissions.inventoryProducts,
                    "post"
                )) {
                Api()
                    .post(
                        "item/brand", {
                            name: this.brand
                        }
                    )
                    .then(res => {
                        //// to show
                        if (res.data) {
                            let brand = {
                                id: res.data.id,
                                name: res.data.name
                            };
                            this.availableBrands.unshift(brand);
                            this.enterBrand = false;

                            this.brand = brand;
                        }
                    })
                    .catch(err => {
                        // delete in firebase
                        /// to show
                        this.message = "Uploading Brand Failed";
                        this.success = true;
                        this.disable = false;

                        setTimeout(() => {
                            this.success = false;
                            this.message = null;
                        }, 2000);
                    });
            } else {
                alert("You aren't allowed to do this");
            }
        },

        addNewBrand() {
            if (this.brand == "Add Brand") {
                this.enterBrand = true;
                this.brand = null;
            } else {
                this.enterBrand = false;
            }
        },

        addVendor() {
            if (this.getUserPermission(
                    this.user.permissions.inventory,
                    "post"
                )) {
                Api()
                    .post(
                        "vendor", {
                            storeName: this.vendor
                        }
                    )
                    .then(res => {
                        //// to show
                        if (res.data) {
                            let vendor = {
                                id: res.data.id,
                                name: res.data.storeName
                            };
                            this.availableVendors.unshift(vendor);
                            this.enterVendor = false;

                            this.vendor = vendor;
                        }
                    })
                    .catch(err => {
                        // delete in firebase
                        /// to show
                        this.message = "Uploading Vendor Failed";
                        this.success = true;
                        this.disable = false;

                        setTimeout(() => {
                            this.success = false;
                            this.message = null;
                        }, 2000);
                    });
            } else {
                alert("You aren't allowed to do this");
            }
        },

        addNewVendor() {
            console.log(this.vendor);
            if (this.vendor == 'Add Vendor') {
                this.enterVendor = true;
                this.vendor = null;
            } else {
                this.enterVendor = false;

            }
        },

        checkActivationItem(event) {
            if (
                (this.subcategories[this.subcategory].active != "1" ||
                    this.categories[this.category].active != "1" ||
                    this.cocategories[this.cocategory].active != "1")) {
                this.active = 0;
                if (this.categories[this.category].active != "1") {
                    alert("This item will remain inactive as it's category is inactive.");
                } else if (this.subcategories[this.subcategory].active != "1") {
                    alert("This item will remain inactive as it's subcategory is inactive.");
                } else if (this.cocategories[this.cocategory].active != "1") {
                    alert("This item will remain inactive as it's cocategory is inactive.");
                }
            }

        },

        checkItem() {
            Api()
                .get("item", {
                    params: {
                        name: this.name,
                        unit: this.unit,
                        qty: this.qty,
                        count: "laoDo"
                    }
                })
                .then(res => {
                    if (res.data) {
                        /// to show
                        this.message =
                            this.name +
                            " exists";
                        this.success = true;

                        setTimeout(() => {
                            this.success = false;
                            this.message = null;
                        }, 2000);
                        this.duplicate = true;
                    } else {
                        this.duplicate = false;
                    }
                });
            return this.duplicate;
        },

        checkSimilarName() {
            this.searchingItem = true;
            this.similarItems = [];
            Api()
                .get("search", {
                    params: {
                        table: "items",
                        data: this.processContent("string", this.name)
                    }
                })
                .then(res => {
                    if (res.data.exactSearch != "" && res.data.exactSearch != null) {
                        /// to show
                        res.data.exactSearch.forEach(doc => {
                            this.similarItems.push(doc);
                        });
                    } else {
                        this.similarItems.push({
                            name: "No item Available"
                        });

                        this.setSimilarId("no", "item");
                    }

                    this.searchingItem = false;
                });
        },
        discountCal() {
            if (
                this.mrp != null &&
                this.currentPrice != null &&
                this.mrp != "" &&
                this.currentPrice != "" &&
                this.currentPrice <= this.mrp
            ) {
                this.discount = (
                    ((this.mrp - this.currentPrice) / this.mrp) *
                    100
                ).toFixed(0);
            } else {
                this.discount = 0;
            }
        },

        generateSKU() {
            let nameCode = this.name.split(" ");
            let nameId = "";
            nameCode.forEach(word => {
                var initialNameLetter = word.substring(0, 1).toUpperCase();

                // checking if word if alphanumeric
                let code = initialNameLetter.charCodeAt(0);
                if (!(code > 47 && code < 58) &&
                    !(code > 64 && code < 91) &&
                    !(code > 96 && code < 123)
                ) {
                    // just ignore that letter to be added to the id
                } else {
                    nameId = nameId + initialNameLetter;
                }
            });

            let cat = this.categories[this.category].name
                .substring(0, 3)
                .toUpperCase()
                .split("");
            let catId = "";

            cat.forEach(word => {
                //var initialNameLetter = word.substring(0, 1).toUpperCase();

                // checking if word if alphanumeric
                let code = word.charCodeAt(0);
                if (!(code > 47 && code < 58) &&
                    !(code > 64 && code < 91) &&
                    !(code > 96 && code < 123)
                ) {
                    // just ignore that letter to be added to the id
                } else {
                    catId = catId + word;
                }
            });

            let subcat = this.subcategories[this.subcategory].name
                .substring(0, 3)
                .toUpperCase()
                .split("");
            let subcatId = "";

            subcat.forEach(word => {
                //var initialNameLetter = word.substring(0, 1).toUpperCase();

                // checking if word if alphanumeric
                let code = word.charCodeAt(0);
                if (!(code > 47 && code < 58) &&
                    !(code > 64 && code < 91) &&
                    !(code > 96 && code < 123)
                ) {
                    // just ignore that letter to be added to the id
                } else {
                    subcatId = subcatId + word;
                }
            });

            let cocat = this.cocategories[this.cocategory].name
                .substring(0, 3)
                .toUpperCase()
                .split("");
            let cocatId = "";

            cocat.forEach(word => {
                //var initialNameLetter = word.substring(0, 1).toUpperCase();

                // checking if word if alphanumeric
                let code = word.charCodeAt(0);
                if (!(code > 47 && code < 58) &&
                    !(code > 64 && code < 91) &&
                    !(code > 96 && code < 123)
                ) {
                    // just ignore that letter to be added to the id
                } else {
                    cocatId = cocatId + word;
                }
            });

            let id = catId + "-" + subcatId + "-" + cocatId; //"-" + nameId

            return id;
        },

        generateTags(tags) {
            let tagCommaSeparated = null;
            tags.forEach((tag, index) => {
                if (index == 0) {
                    tagCommaSeparated = tag.name;
                } else {
                    tagCommaSeparated = tagCommaSeparated + ', ' + tag.name;
                }
            });

            return tagCommaSeparated;

        },

        getBrands() {
            let flag = 0;
            Api().get("item/brand", {
                params: {
                    order: 'name',
                    orderWay: 'ASC'
                }
            }).then(res => {
                res.data.forEach((doc, index) => {
                    let brand = {
                        id: doc.id,
                        name: doc.name
                    };
                    this.availableBrands.push(brand);
                });
            });
        },

        getNameDetails(from) {
            if (from == 'add') {
                if (this.showEdit != -1) {
                    this.showEdit = 0;
                }
                this.checkItem();
            }
            this.checkSimilarName();
        },
        getUnits() {
            this.units = [];
            Api()
                .get("item/units")
                .then(res => {
                    if (res.data) {
                        res.data.forEach(doc => {
                            this.units.push(doc);
                        });
                    }
                })
                .catch(err => {
                    this.units = [];
                    console.log(err);
                });
        },

        getVendors() {
            this.availableVendors = [];
            Api().get("vendor", {
                params: {
                    active: 1,
                    deleteStatus: 0,
                    order: 'storeName',
                    orderWay: 'ASC'
                }
            }).then(res => {
                res.data.forEach((doc, index) => {
                    this.availableVendors.push(doc);
                });
            });
        },

        fetchCategories() {
            let categories = [];
            Api()
                .get("cat")
                .then(res => {
                    res.data.forEach(doc => {
                        const cat = {
                            name: doc.name,
                            subcatid: doc.subcatcode,
                            active: doc.active
                        };
                        categories.push(cat);
                    });
                    this.categories = categories;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        processContent(type, data) {
            if (data) {
                if (type == "string") {
                    return data.trim();
                } else if (type == "html") {
                    let lastIndex = data.lastIndexOf("<p>&nbsp;</p>");
                    if (lastIndex != -1 && data.length - 14 == lastIndex) {
                        // will remove everywhere
                        // data = data.replace(
                        //   new RegExp(this.escapeRegExp("<p>&nbsp;</p>"), "g"),
                        //   ""
                        // );
                        data = data.substring(0, lastIndex);
                    }
                    return data;
                }
            }
            return data;
        },

        profitLossCal() {
            if (
                this.currentPrice != null &&
                this.msp != null &&
                this.currentPrice != "" &&
                this.msp != ""
            ) {
                this.profitLoss = this.currentPrice - this.msp;
                this.profitLossPer = ((this.profitLoss / this.msp) * 100).toFixed(0);
                if (this.profitLoss < 0) {
                    this.profitLossPer = Math.abs(this.profitLossPer);
                    this.profitLoss = Math.abs(this.profitLoss);
                    $("#profitLossModal").modal("show");
                }
            }
        },

        priceBandCalculator(change) {
            //calculating price band
            // if (change == "priceBand" && this.msp != null && this.msp != "" && !this.currentPrice) {
            //     //calculating current price
            //     let add = Math.round(this.msp * (this.priceBand / 100));
            //     this.currentPrice = this.msp + add;

            // } else if (change == "msp" && this.priceBand != null) {
            //     //calculating current price
            //     let add = Math.round(this.msp * (this.priceBand / 100));
            //     this.currentPrice = this.msp + add;
            //     console.log(add);
            // }
            // console.log('add');

            this.discountCal();
        },

        searchItem() {
            if (this.searchProductID) {
                this.disable = true;
                this.disableMessage = "Checking our Databases...";
                Api()
                    .get("item", {
                        params: {
                            productId: this.searchProductID.trim(),
                            tags: 'fetch',
                            vendorDetail: 'fetch',
                            category: "fetch",
                        }
                    })
                    .then(res => {
                        if (res.data.length > 0) {
                            res.data.forEach(doc => {
                                this.productId = doc.productId;
                                this.itemId = doc.itemId;
                                this.currentPrice = doc.currentPrice;
                                this.name = doc.name;
                                this.image = doc.image;
                                // if some image is preexisiting remove that
                                this.media[0] = null;
                                this.discount = doc.discount;
                                this.unit = doc.unit;
                                this.unit2 = doc.unit2;
                                this.color = doc.color;
                                this.mrp = doc.MRP;
                                this.stock = doc.stock;
                                this.itemId = doc.itemId;
                                this.sku = doc.sku;
                                this.disVal = doc.dis_val;
                                this.disPriority = doc.disPriority; //dou;
                                this.description = doc.description;
                                this.ingredients = doc.ingredients;
                                this.features = doc.features;
                                this.msp = doc.msp;
                                this.qty = doc.qty;
                                this.mfgDate = doc.mfgDate;
                                this.expiryDate = doc.expiryDate;
                                this.shelfLife = doc.shelfLife;
                                this.brand = doc.item_brand;
                                this.address = doc.address;
                                this.similarProductId = doc.similarProductId;
                                this.uploadedBy = doc.uploadedBy;
                                this.uploadedAt = doc.uploadedAt;
                                this.updatedAt = doc.updatedAt;
                                this.maxLimit = doc.maxLimit;
                                this.soldUnder = doc.soldUnder;
                                this.vendor = doc.vendor_detail;
                                this.tags = this.generateTags(doc.tags);
                                this.priceBand = Math.round(
                                    ((this.currentPrice - this.msp) / this.currentPrice) * 100
                                );
                                this.active = doc.active;

                                if (doc.dropShipAddress) {
                                    this.dropShip = true;
                                    this.dropShipAddress = doc.dropShipAddress;
                                }
                                //setting category
                                this.categories.forEach((cat, index) => {
                                    let flag = false;
                                    if (doc.cocategory.subcategory.category.subcatcode == cat.subcatid && !flag) {
                                        this.category = index;
                                        flag = true;
                                    }
                                });
                                this.subcategories = [doc.cocategory.subcategory];
                                this.subcategory = 0;
                                this.cocategories = [doc.cocategory];
                                this.cocategory = 0;




                            });
                            this.disable = false;
                            this.disableMessage =
                                "Uploading Data, Kripya Shanti banaye rkhe...";
                        } else {
                            this.message = "This product Don't exist in database";
                            this.success = true;

                            setTimeout(() => {
                                this.success = false;
                                this.message = null;
                            }, 2000);

                            this.disable = false;
                            this.disableMessage =
                                "Uploading Data, Kripya Shanti banaye rkhe...";
                        }
                    });
            } else {
                // bs  bhai ho gya
            }
        },

        setCocategory() {
            if (this.cocategories[this.cocategory].active == "0") {
                this.active = "0";
            }
            this.itemId = this.cocategories[this.cocategory].itemId;
            this.priceBand = this.cocategories[this.cocategory].priceBandValue;
        },

        setCurrentPrice() {
            this.discountCal();
            this.pricaBandCalculator("currentPrice");
        },

        setMsp() {
            this.profitLossCal();
            this.pricaBandCalculator("msp");
        },

        setMrp() {
            this.discountCal();
            this.pricaBandCalculator("mrp");
        },

        setSimilarId(type, data) {
            if (type) {
                if (type == "id") {
                    this.similarProductId = this.similarItems[data].similarProductId;
                } else if (type == "productId") {
                    this.searchingItem = true;

                    let id = data.target.value;
                    Api()
                        .get("item", {
                            params: {
                                productId: id
                            }
                        })
                        .then(res => {
                            res.data.forEach(doc => {
                                this.similarProductId = doc.similarProductId;
                            });
                            this.searchingItem = false;
                        });
                } else {
                    this.searchingItem = true;
                    Api()
                        .get("item", {
                            params: {
                                max: "similarProductId"
                            }
                        })
                        .then(res => {
                            this.similarProductId = Number(res.data) + 1;
                            this.searchingItem = false;
                        });
                }
            }
        },

        validate() {
            this.errors = [];
            if (
                this.itemId &&
                this.name &&
                this.discount >= 0 &&
                this.currentPrice > 0 &&
                this.unit &&
                this.mrp > 0 &&
                this.msp > 0 &&
                this.stock >= 0 &&
                (this.disVal == 1 || this.disVal == 0) &&
                this.qty &&
                this.similarProductId > 0 &&
                this.brand
            ) {
                return true;
            } else {
                Vue.set(
                    this.errors,
                    0,
                    "Make sure you have added all the required fields. Contact the developer if you face any issue."
                );

                if (!this.name) {
                    Vue.set(this.errors, 1, "Enter Valid Name");
                }

                if (!this.itemId) {
                    Vue.set(this.errors, 2, "Choose Category");
                }

                if (!this.unit) {
                    Vue.set(this.errors, 3, "Choose Valid Unit");
                }
                if (this.qty <= 0) {
                    Vue.set(this.errors, 4, "Add a Quantity");
                }
                if (this.stock < 0) {
                    Vue.set(this.errors, 5, "Add stock");
                }
                if (this.msp <= 0) {
                    Vue.set(this.errors, 6, "MSP can't be 0");
                }
                if (this.currentPrice <= 0) {
                    Vue.set(this.errors, 7, "Current Price can't be 0");
                }
                if (this.mrp <= 0) {
                    Vue.set(this.errors, 8, " MRP can't be 0");
                }
                if (this.disVal != 1 && this.disVal != 0) {
                    Vue.set(this.errors, 9, "Choose valid Discount Value");
                }
                if (!this.similarProductId) {
                    Vue.set(this.errors, 11, "Choose it's variant");
                }
                if (this.brand == null || this.brand.length == 0) {
                    Vue.set(this.errors, 12, "Choose a Brand");
                }

                return false;
            }
        }
    },
}


