              <span>Current Package</span>
              <h4 v-if="user.package == 1">Data Upload</h4>
              <h4 v-if="user.package == 3">Data + Image Upload</h4>
              <h4 v-else>₹0</h4>
            </div>
          </div>
        </div>
      </div>
    </div>
