<template>
  <div>
    <!-- header -->
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <div class="pl-4">
          <i class="far fa-list-alt"></i>
          <h5 class="pl-2 d-inline-block">
            <strong>
              <span v-if="status == 3">READY FOR PICKUP</span>
              <span v-if="status == 4">OUT FOR DELIVERIES</span>
              <span v-if="status == 5">SUCCESSFUL</span>
              <span v-if="status == 7">RETURN PICKED</span>
              <span v-if="status == 6">RETURNED</span>
              <span v-if="status == 8">FAILED</span>
              <span v-if="status == 9">UNDELIVER FAIL 1</span>
              <span v-if="status == 10">UNDELIVER FAIL 2</span>
              DELIVERIES
            </strong>
          </h5>
        </div>
      </div>
      <div class="col-6">
        <form
          class="form-inline float-right"
          v-if="
            status && (status == 4 || status == 3 || status == 5 || status == 6 || status == 9 || status == 10) && this.getUserPermission(this.user.permissions.salesOrder,'put')
          "
        >
          <span class="font-weight-semibold pr-3">Select Delivery Status</span>
          <select
            v-model="action"
            name="status"
            class="custom-select custom-select-sm"
            @change="updateAction()"
          >
            <option disabled="disabled" selected="selected">Choose Action</option>

            <option value="o1">Cancel</option>
            <option value="4" v-if="status == 3">Out For Delivery</option>
            <option value="5" v-if="status == 4">Delivered</option>
            <option value="o2" v-if="status == 5">Reuqest Return</option>
            <option value="7" v-if="status == 6">Return Picked</option>
            <option value="8" v-if="status != 9">Failed</option>
            <option value="delete">Delete</option>
          </select>
        </form>
      </div>
    </div>
    <hr class="pl-4 pr-4" />
    <div class="table-responsive table-scroll mt-4">
      <table class="table table-hover table-sm table-borderless table-content text-left">
        <thead>
          <tr>
            <th
              v-if="
                status &&
                  (status == 4 || status == 3 || status == 5 || status == 6 || status == 9 || status == 10)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck"
                    @click="selectAll()"
                    v-model="select"
                  />
                  <label class="custom-control-label" for="customCheck"></label>
                </div>
              </form>
            </th>
            <th style="width:30px">Order Id</th>
            <th>Order By</th>
            <th>Products</th>
            <th>Deliver Items</th>
            <th>Amount</th>
            <th v-if="status!=9 && status!=10">Payment Status</th>
            <th v-else>Reason</th>
            <th>Delivery Status</th>
            <th>Delivery By</th>
            <th v-if="status == 4 || status == 5">Expected Delivery By</th>
            <!-- <th>Inventory Address</th> -->
            <th>Delivery Address</th>
            <th>Order At</th>
            <th v-if="status==9 || status==10">Undelivered At</th>
            <th></th>
          </tr>
        </thead>
        <tbody v-if="deliveries.length > 0">
          <tr v-for="(delivery,index) in deliveries" :key="delivery.id">
            <td
              v-if="
                status &&
                  (status == 4 || status == 3 || status == 5 || status == 6 || status == 9 || status == 10)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    :id="delivery.deliveryId"
                    v-model="delivery.checked"
                    :value="delivery.deliveryId"
                  />
                  <label class="custom-control-label" :for="delivery.deliveryId"></label>
                </div>
              </form>
            </td>
            <td style="width:30px">
              {{ delivery.order.id.substring(0, 4) + "..." }}
              <i
                class="far fa-copy pl-1"
                style="cursor:pointer"
                @click="copyOrderId(delivery.order.id)"
              ></i>
              <input type="hidden" :value="delivery.order.id" :id="delivery.order.id" />
            </td>
            <td>
              <span>{{ delivery.order.user_address.name }}</span>

              <br />
              <span
                v-if="delivery.order.user_address.mobileNumber"
              >{{ delivery.order.user_address.mobileNumber }}</span>
              <span v-else>N/A</span>
              <br />
              <small>
                <b>Registered By:</b>
                <span v-if="delivery.order.user.mobileNumber">{{ delivery.order.user.mobileNumber }}</span>
                <span v-if="delivery.order.user.email">{{ delivery.order.user.email }}</span>
              </small>
            </td>
            <td>
              <div v-if="delivery.order.products.length > 2">
                <span
                  v-for="product in delivery.order.products.slice(0, 2)"
                  :key="product.productId"
                >
                  <img :src="product.image" class="img-fluid content-image" /> +
                </span>
                <span>
                  <b>{{ delivery.order.products.length - 2 }}</b>
                </span>
              </div>
              <div v-else>
                <span v-for="product in delivery.order.products" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" />
                </span>
              </div>
            </td>
            <td>{{ delivery.order.availableItems }}</td>
            <td>{{ delivery.order.totalPayableAmount }}</td>
            <td v-if="status!=9 && status!=10">
              <span
                class="bagde badge-waiting pl-1 pr-1"
                v-if="delivery.order.paidAmount == 0"
              >Not Paid</span>
              <span
                class="bagde badge-paid pl-1 pr-1"
                v-else-if="
                  delivery.order.paidAmount == delivery.order.totalPayableAmount
                "
              >Paid</span>
              <span class="bagde badge-failed pl-1 pr-1" v-else>
                Pending :
                {{
                delivery.order.totalPayableAmount - delivery.order.paidAmount
                }}
              </span>
            </td>
            <td v-else>
              <span v-if="status==9 && delivery.undelivered1">{{delivery.undelivered1.reason}}</span>
              <span v-else-if="status==10 && delivery.undelivered2">{{delivery.undelivered2.reason}}</span>
              <span v-else>N/A</span>
            </td>
            <td>
              <span class="badge badge-cancelled pl-2 pr-2" v-if="delivery.status == 1">Cancelled</span>
              <span
                class="badge badge-processing pl-2 pr-2"
                v-if="delivery.status == 2"
              >Not yet Ready</span>
              <span class="badge badge-packed pl-2 pr-2" v-if="delivery.status == 3">Ready To Pickup</span>
              <span class="badge badge-shipping pl-2 pr-2" v-if="delivery.status == 4">Shipping</span>
              <span class="badge badge-delivered pl-2 pr-2" v-if="delivery.status == 5">Delivered</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-if="delivery.status == 6"
              >Return PickUp</span>
              <span class="badge badge-return pl-2 pr-2" v-if="delivery.status == 7">Returned</span>
              <span class="badge badge-failed pl-2 pr-2" v-if="delivery.status == 8">Failed</span>
              <span
                class="badge badge-failed pl-2 pr-2"
                v-if="delivery.status == 9"
              >Undelivered Fail 1</span>
              <span
                class="badge badge-failed pl-2 pr-2"
                v-if="delivery.status == 10"
              >Undelivered Fail 2</span>
            </td>
            <td class="text-dark">{{ delivery.deliveryBy }}</td>
            <td
              class="text-dark"
              v-if="status == 4 || status == 5"
            >{{ delivery.estDeliveryTime[1] }}</td>
            <!-- <td class="text-dark">
              {{ delivery.order.inventory_address.name }}
            </td>-->
            <td class="text-dark">
              <!-- {{ delivery.order.user_address.addressName }}, -->
              {{ delivery.order.user_address.houseOfficeNo }},
              {{ delivery.order.user_address.street }},
              {{ delivery.order.user_address.city }},
              {{ delivery.order.user_address.landmark }},
              {{ delivery.order.user_address.pincode }}
            </td>
            <td>
              {{ delivery.date }}
              <small>{{ delivery.time }}</small>
            </td>
            <td class="text-dark" v-if="status==9 || status==10">
              <span v-if="status==9 && delivery.undelivered1">{{delivery.undelivered1.created_at}}</span>
              <span v-if="status==10 && delivery.undelivered2">{{delivery.undelivered2.created_at}}</span>
              <span v-else>N/A</span>
            </td>
            <td class="text-dark">
              <div class="dropdown dropleft dropleft">
                <a
                  href="#"
                  class="float-right"
                  role="button"
                  :id="'dropdownMenuLink'.index"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fa fa-lg fa-ellipsis-v" aria-hidden="true"></i>
                </a>
                <div class="dropdown-menu" :aria-labelledby="'dropdownMenuLink'.index">
                  <router-link
                    :to="{
                      name: 'OrderReturnSlip',
                      params: {
                        slipType: 'returnSlip',
                        orderId: delivery.order.id
                      }
                    }"
                    tag="a"
                    class="pr-1 dropdown-item"
                    target="_blank"
                    v-if="status == 2 || status == 9"
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Return Slip</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'OrderBill',
                      params: { slipType: 'bill', orderId: delivery.order.id }
                    }"
                    tag="a"
                    target="_blank"
                    class="pr-1 dropdown-item"
                    v-else
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Generate Bill</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'ViewOrder',
                      params: { orderId: delivery.order.id }
                    }"
                    target="_blank"
                    class="pr-1 dropdown-item"
                  >
                    <i class="fa fa-eye pr-2"></i>
                    <span class="pl-2 text-center">View</span>
                  </router-link>
                  <a
                    :href="
                      'https://www.google.com/maps/search/' +
                        delivery.order.ordererLocation
                    "
                    target="_blank"
                    class="dropdown-item"
                  >
                    <i class="fa fa-map-pin pr-2" aria-hidden="true"></i>
                    <span class="pl-2 text-center">Locate</span>
                  </a>

                  <!-- undelivered -->
                  <span
                    class="dropdown-item"
                    data-toggle="modal"
                    data-target="#undeliveredModal"
                    @click="selectOrderForUndeliver(index)"
                    v-if="status == 2 || status == 3 || status == 4 || status == 5"
                  >Undelivered</span>
                  <span
                    class="dropdown-item"
                    data-toggle="modal"
                    data-target="#undeliveredModal"
                    @click="selectOrderForUndeliver(index)"
                    v-else-if="status==9 && delivery.undelivered1 && delivery.undelivered1.secondFailApplicable==1"
                  >Undelivered Fail 2</span>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="9" class="text-center">
              <div>
                <img
                  class="card-img-top img-fluid"
                  src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                  alt="Card image cap"
                  style="width:20em"
                />
                <div class="card-body">
                  <p class="card-text">Oops! There ain't any delivery Made</p>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- undelivered modal -->
    <div id="undeliveredModal" class="modal fade" role="dialog" v-if="selectedOrder">
      <div class="modal-dialog modal-dialog-scrollable">
        <!-- Modal content-->
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold">
              <i class="fas fa-clipboard-list pr-3"></i>Undelivered
            </h5>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div
            class="modal-body"
            style="position:relative;"
            v-if="selectedOrder && ((selectedOrder.status==9 && selectedOrder.undelivered1.secondFailApplicable==1) || selectedOrder.status!=10)"
          >
            <div>
              <label>Order Id</label>
              <div>{{ selectedOrder.order.id }}</div>
            </div>
            <!-- second delivery applicable -->
            <div v-if="selectedOrder.status!=9">
              <div class="custom-control custom-switch">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="switch1"
                  v-model="secondDeliveryApplicable"
                  true-value="1"
                  false-value="0"
                  :disabled="disable"
                />
                <label class="custom-control-label" for="switch1">Is second Delivery Applicable?</label>
                <span
                  href="#"
                  data-toggle="tooltip"
                  data-placement="top"
                  title="Do you want to try to deliver again?"
                >
                  <i class="fa fa-question-circle" aria-hidden="true"></i>
                </span>
              </div>
            </div>
            <!-- send user notification -->
            <div>
              <div class="custom-control custom-switch">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="switch2"
                  v-model="sendUserNotification"
                  true-value="1"
                  false-value="0"
                  :disabled="disable"
                />
                <label class="custom-control-label" for="switch2">Send User Notification</label>
                <span
                  href="#"
                  data-toggle="tooltip"
                  data-placement="top"
                  title="Do you want to send user notification about this"
                >
                  <i class="fa fa-question-circle" aria-hidden="true"></i>
                </span>
              </div>
            </div>
            <div>
              <label>Reason</label>
              <textarea class="form-control form-control-sm" v-model="reason" rows="3"></textarea>
            </div>
          </div>
          <div
            class="modal-body"
            style="position:relative;"
            v-else-if="selectedOrder && selectedOrder.status==9 && selectedOrder.undelivered1.secondFailApplicable!=1"
          >Not Applicable</div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary"
              @click="undeliverOrder(9)"
              v-if="status!=9"
            >Update to Undeliver Fail 1</button>
            <button
              type="button"
              class="btn btn-primary"
              @click="undeliverOrder(10)"
              v-else-if="status==9 && selectedOrder.undelivered1.secondFailApplicable==1"
            >Update to Undeliver Fail 2</button>

            <button type="button" class="btn btn-danger btn-sm" data-dismiss="modal">
              <i class="fas fa-times-circle pr-2"></i>Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <DeliveryFooter />
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>
<script>
import Vue from "vue";
import DeliveryFooter from "./DeliveryFooter.vue";
import { bus } from "@/main";
import store from "@/store/store";
import Api from "@/services/Api.js";

export default {
  name: "DashBoard",
  components: {
    DeliveryFooter
  },
  props: {
    status: Number,
    newDeliveries: Array,
    disable: Boolean
  },

  data() {
    return {
      deliveryStatus: [],
      search: null,
      searchArray: [],

      select: false,
      action: null,

      sendUserNotification: 0,
      secondDeliveryApplicable: 0,
      reason: null,
      selectedOrder: null,
      selectedIndex: null,

      message: null,
      success: null,
      loading: this.disable
    };
  },

  computed: {
    deliveries() {
      this.searchArray = this.newDeliveries;
      return this.newDeliveries;
    }
  },

  created() {
    //this.getOrderStatus();
    bus.$on("paginateDelivery", data => {
      this.paginateDelivery(data["page"]);
    });
  },

  methods: {
    copyOrderId(id) {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        let orderID = "#" + id;
        let idToCopy = document.querySelector(orderID);
        idToCopy.setAttribute("type", "text");
        idToCopy.select();

        try {
          let successful = document.execCommand("copy");
          let message = successful ? "Copied!" : "Unable to Copy!";
          alert(message);
        } catch (err) {
          alert("Whoops! unable to copy");
        }

        idToCopy.setAttribute("type", "hidden");
        window.getSelection().removeAllRanges();
      } else {
        alert("You aren't allowed to do this");
      }
    },
    updateAction() {
      if (
        this.getUserPermission(this.user.permissions.salesOrder, "put") ||
        this.getUserPermission(this.user.permissions.salesOrder, "put")
      ) {
        this.deliveries.forEach((delivery, index) => {
          if (delivery.checked) {
            if (
              this.action == "delete" &&
              this.getUserPermission(this.user.permissions.salesOrder, "delete")
            ) {
              if (confirm("You are about to delete " + delivery.order.id)) {
                this.loading = true;
                let url = "order/" + delivery.order.id;
                Api()
                  .post(url, {
                    _method: "DELETE"
                  })
                  .then(res => {
                    if (res.data) {
                      this.message = "Order successfully deleted";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      this.deliveries.splice(index, 1);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    }
                  })
                  .catch(err => {
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            } else if (
              this.getUserPermission(this.user.permissions.salesOrder, "put")
            ) {
              if (this.action == "o1" || this.action == "o2") {
                this.loading = true;
                let url = "order/" + deliver.order.id;
                let action = 0;
                if (this.action == "o1") {
                  action = 1;
                } else {
                  action = 2;
                }
                Api()
                  .post(url, {
                    _method: "PUT",
                    reason: "Done from webapp",
                    preferredPaymentMode: 1,
                    status: action
                  })
                  .then(res => {
                    this.message = "Successfully Updated and moved";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    //remove that order after save
                    if (res.data == "Success") {
                      this.deliveries.splice(index, 1);
                    }

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  })
                  .catch(err => {
                    this.message = "Some error occured";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              } else {
                this.loading = true;
                let url = "delivery/" + delivery.deliveryId;
                Api()
                  .post(url, {
                    _method: "PUT",
                    status: this.action
                  })
                  .then(res => {
                    this.message = "Successfully Updated and moved";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    //remove that order after save
                    if (res.data == "Success") {
                      this.deliveries.splice(index, 1);
                    }

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  })
                  .catch(err => {
                    this.message = "Some error occured";
                    this.success = true;
                    this.loading = false;
                    console.log(err.message);

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            }
          }
        });
      } else {
        alert("You are not allowed to make changes");
      }
    },

    paginateDelivery(page) {
      this.$emit("fetchDeliveries", page);
    },

    searching() {
      if (this.search) {
        this.deliveries = this.searchArray.filter(
          v => v.orderId.indexOf(this.search) != -1
        );
      } else {
        this.deliveries = this.searchArray;
      }
    },

    selectAll() {
      this.deliveries.forEach(delivery => {
        if (!this.select) {
          delivery.checked = true;
          //Vue.set(order, checked, true);
        } else {
          delivery.checked = false;
        }
      });
    },

    selectOrderForUndeliver(index) {
      this.selectedIndex = index;
      this.selectedOrder = this.deliveries[index];
    },

    undeliverOrder(status) {
      this.loading = true;
      let url = "delivery/" + this.selectedOrder.deliveryId;
      if (status == 9) {
        Api()
          .post(url, {
            _method: "PUT",
            status: status,
            reason: this.reason,
            sendUserNotification: this.sendUserNotification,
            secondFailApplicable: this.secondDeliveryApplicable
          })
          .then(res => {
            this.message = "Successfully Updated and moved";
            this.success = true;
            this.loading = false;
            this.action = null;

            //remove that order after save
            if (res.data == "Success") {
              this.deliveries.splice(this.selectedIndex, 1);
              this.selectedOrder = null;
              this.selectedIndex = null;
            }

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            console.log(err.message);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      } else {
        Api()
          .post(url, {
            _method: "PUT",
            status: status,
            reason: this.reason,
            sendUserNotification: this.sendUserNotification
          })
          .then(res => {
            this.message = "Successfully Updated and moved";
            this.success = true;
            this.loading = false;
            this.action = null;

            //remove that order after save
            if (res.data == "Success") {
              this.deliveries.splice(this.selectedIndex, 1);
              this.selectedOrder = null;
              this.selectedIndex = null;
            }

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            console.log(err.message);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



