<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>Vendors</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small"></div>
            </div>
            <!-- content -->
            <div class="table-responsive table-scroll mt-4">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>Sr.No</th>
                    <th>Store Name</th>
                    <th>Vendor Name</th>
                    <th>Mobile Number</th>
                    <th>Address</th>
                    <th>Supply From</th>
                    <th>Supply Till</th>
                    <th>No of current Items</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody v-if="vendors.length > 0">
                  <tr v-for="(vendor, index) in vendors" :key="index + 1">
                    <td>{{index+1}}</td>
                    <td>
                      <span v-if="vendor.storeName">{{vendor.storeName}}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.vendorName">{{vendor.vendorName}}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.mobileNumber">{{vendor.mobileNumber}}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.address">{{vendor.address}}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.startedFrom">
                        {{vendor.startedFrom[0]}}
                        <br />
                        {{vendor.startedFrom[1]}}
                      </span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.till">
                        {{vendor.till[0]}}
                        <br />
                        {{vendor.till[1]}}
                      </span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="vendor.items_count">{{vendor.items_count}}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <router-link
                        :to="{
                          name: 'VendorItems',
                          params: { vendorId: vendor.id },
                        }"
                        tag="a"
                        target="_blank"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Know about items"
                      >
                        <i class="fa fa-shopping-cart text-dark pr-1"></i>
                      </router-link>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr>
                    <td colspan="9" class="text-center">
                      <div>
                        <img
                          class="card-img-top img-fluid"
                          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                          alt="Card image cap"
                          style="width: 20em;"
                        />
                        <div class="card-body">
                          <p class="card-text">There's no vendor!</p>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { bus } from "@/main";
import Api from "@/services/Api.js";

export default {
  name: "Vendors",
  data() {
    return {
      vendors: [],
      disable: false,
      message: null,
      success: false
    };
  },

  mounted() {
    this.fetchVendors();
  },

  methods: {
    fetchVendors() {
      if (
        this.getUserPermission(this.user.permissions.inventory, "get") &&
        this.getUserPermission(this.user.permissions.marketing, "get")
      ) {
        this.vendors = [];

        this.disable = true;
        Api()
          .get("vendor", {
            params: {
              deleteStatus: 0,
              order: "storeName",
              orderWay: "asc"
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let startedFrom = this.getDateTime(doc.supplyStartedOn);
              let till = this.getDateTime(doc.supplyEndedOn);
              doc.startedFrom = startedFrom;
              doc.till = till;
              this.vendors.push(doc);
            });
            this.disable = false;
          });
      }
    }
  }
};
</script>



