<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>PUSH NOTIFICATIONS</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <form class="form-inline float-right">
                  <span class="font-weight-semibold pr-3">To</span>
                  <select
                    v-model="sendTo"
                    name="status"
                    class="custom-select"
                    @change="fetchNotifications(1)"
                  >
                    <option disabled="disabled" selected="selected">Choose Recievers</option>
                    <option value="1">All</option>
                    <option value="2">Admins</option>
                    <option value="3">All Users</option>
                    <option value="4">Logged In Users</option>
                    <option value="5">Anonymous users</option>
                  </select>
                </form>
              </div>
            </div>
            <!-- <hr class="pl-4 pr-4" /> -->
            <!-- content -->
            <div>
              <div class="table-responsive table-scroll mt-4">
                <table class="table table-hover table-sm table-borderless table-content text-left">
                  <thead>
                    <tr>
                      <th>Image</th>
                      <th>Type</th>
                      <th>Content</th>
                      <th>Sent To</th>
                      <th>Schedule</th>
                      <th>Interval</th>
                      <th>Start At</th>
                      <th>Expires At</th>
                      <th>Upload Info</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="notifications.length > 0">
                    <tr v-for="(notification, index) in notifications" :key="index + 1">
                      <td>
                        <img :src="notification.image" v-if="notification.image" width="50" />
                        <span v-else>N/A</span>
                      </td>
                      <td>{{ notification.type }}</td>
                      <td>{{ notification.content }}</td>
                      <td>{{ notification.sendTo }}</td>
                      <td>{{ notification.schedule }}</td>
                      <td>{{ notification.interval }}</td>
                      <td>
                        {{ notification.startAt[0] }}
                        {{ notification.startAt[1] }}
                      </td>
                      <td>
                        <span v-if="notification.expiryAt != null">
                          {{ notification.expiryAt[0] }}
                          {{ notification.expiryAt[1] }}
                        </span>
                        <span v-else>Always On</span>
                      </td>
                      <td>
                        <span v-if="notification.uploadedBy != null">
                          {{
                          notification.uploadedBy.name
                          }}
                        </span>
                        At
                        {{ notification.created_at[0] }}
                        {{ notification.created_at[1] }}
                      </td>
                      <td class="text-dark">
                        <!-- <div v-if="notification.interval != 'One Time'"> -->
                        <span
                          v-if="
                            notification.schedule == 'none' &&
                            notification.interval == 'None'
                          "
                        >
                          <i class="fas fa-ban" title="This can't be edited"></i>
                        </span>
                        <span v-else>
                          <router-link
                            :to="{
                              name: 'EditPushNotification',
                              params: {
                                id: notification.id,
                              },
                            }"
                            class="text-black fa fa-pencil-alt pl-2"
                            tag="a"
                            target="_blank"
                            title="edit this item"
                          ></router-link>
                        </span>
                        <span>
                          <a
                            class="fa fa-trash text-dark"
                            href="#"
                            data-toggle="modal"
                            data-target="#deleteItem"
                            data-placement="bottom"
                            title="delete this item"
                            @click="deleteNotification(index)"
                          ></a>
                        </span>
                      </td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="9" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                            alt="Card image cap"
                            style="width: 20em;"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! There ain't any notifications here</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <notificationFooter />
              <Loader :disable="disable" :success="success" :message="message" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { bus } from "@/main";
import store from "@/store/store";
import notificationFooter from "./Footer";
import Api from "@/services/Api.js";

export default {
  name: "ViewPushNotification",
  components: {
    notificationFooter
  },
  data() {
    return {
      notifications: [],
      sendTo: 1,
      disable: false,
      message: null,
      success: false
    };
  },

  mounted() {
    this.fetchNotifications(1);
  },

  created() {
    bus.$on("paginateNotification", data => {
      this.fetchNotifications(data["page"]);
    });
  },

  computed: {
    size() {
      return store.state.notificationPageSize;
    }
  },

  methods: {
    deleteNotification(index) {
      if (
        confirm("You are about to delete notification") &&
        this.getUserPermission(this.user.permissions.marketing, "delete")
      ) {
        this.disable = true;
        Api()
          .post("user/notification/push/" + this.notifications[index].id, {
            _method: "delete"
          })
          .then(res => {
            if (res.data == "Success") {
              this.notifications.splice(index, 1);

              this.message = "Deleted successfully";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              alert(res.data);
            }
          });
      } else {
        alert("You aren't allowed to do this");
      }
    },
    fetchNotifications(page) {
      if (this.getUserPermission(this.user.permissions.marketing, "get")) {
        if (!page) {
          page = 1;
        }
        this.disable = true;
        Api()
          .get("user/notification/push", {
            params: {
              sendTo: this.sendTo,
              deleteStatus: 0,
              notificationSchedule: "fetch",
              notificationInterval: "fetch",
              notificationType: "fetch",
              receiver: "fetch",
              uploader: "fetch",
              order: "created_at",
              orderWay: "desc",
              page: page,
              limit: this.size
            }
          })
          .then(res => {
            let notificationList = [];
            res.data.data.forEach(doc => {
              let startDate = this.getDateTime(doc.startDate);
              let expiryDate = null;
              if (doc.expiryDate) {
                expiryDate = this.getDateTime(doc.startDate);
              }
              let created_at = this.getDateTime(doc.created_at);

              let notification = {
                id: doc.id,
                schedule: doc.notification_schedule.name,
                interval: doc.notification_interval.name,
                type: doc.notification_type.name,
                sendTo: doc.receiver.name,
                uploadedBy: doc.uploader,
                startAt: startDate,
                expiryAt: expiryDate,
                created_at: created_at,
                content: doc.content,
                image: doc.image,
                active: doc.active,
                deleteStatus: doc.deleteStatus
              };
              notificationList.push(notification);

              store.dispatch("patchnotificationPageCount", res.data.last_page);
              store.dispatch(
                "patchnotificationPageNumber",
                res.data.current_page
              );
              store.dispatch("patchtotalNotifications", res.data.total);
            });

            this.notifications = notificationList;
          });

        this.disable = false;
      } else {
        alert("You aren't allowed to do this");
      }
    }
  }
};
</script>



