import axios from "axios";
import {
    domain,
    ContentType,
    XRequestedWith
} from "../store/variable.js";
import store from "../store/store";

export default () => {
    let axiosObj = axios.create({
        baseURL: domain
    });

    const token = store.state.user.uuid;

    axiosObj.defaults.headers.common["Accept"] = "application/json, text/javascript, */*; q=0.01";
    axiosObj.defaults.headers.common["from"] = "webapp";
    axiosObj.defaults.headers.common["X-api-token"] = token;
    axiosObj.defaults.headers.post["Content-Type"] = ContentType;
    axiosObj.defaults.headers.post["X-Requested-With"] = XRequestedWith;
    axiosObj.defaults.withCredentials = true;

    return axiosObj;
}


