<script>
import Api from "@/services/Api.js";

export default {
  name: "Sidebar",
  data() {
    return {
      isLoggedIn: false,
      inventoryCurrentItems: 0,
      inventoryOutOfStock: 0,
      inventoryInactiveItems: 0,
      inventoryLowStock: 0,

      orderNew: 0,
      orderConfirmed: 0,
      orderShipping: 0,
      orderDelivered: 0,
      orderReturned: 0,
      orderReturnedApprove: 0,
      orderProcessing: 0,
      orderFailed: 0,
      orderCancelled: 0,
      orderRequestedReturn: 0,
      totalOrders: 0,

      deliverySuccess: 0,
      deliveryReady: 0,
      totalDeliveries: 0,
      deliveryOutForDelivery: 0,
      undeliverFailOne: 0,
      undeliverFailTwo: 0,

      totalCoupons: 0,

      appUsers: 0,
      webappUploaders: 0,
      totalUserFeedbacks: 0
    };
  },
  created() {
    if (this.user) {
      this.isLoggedIn = true;
    }
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        {
          this.countInventoryItems();
          this.countInventoryOutOfStock();
          this.countInventoryInactiveItems();
          this.countInventoryLowStock();

          //orders
          this.countTotalOrders();
          this.countOrderConfirmed();
          this.countOrderReturned();
          this.countOrderDelivered();
          this.countOrderShipping();
          this.countOrderProcessing();
          this.countOrderFailed();
          this.countOrderCancelled();
          this.countOrderRequestedReturn();
          this.countOrderReturnApproved();

          this.countTotalDeliveries();
          this.countDeliverySuccess();
          this.countDeliveryReady();
          this.countDeliveryOutForDelivery();
          this.countUndeliverFailOne();
          this.countUndeliverFailTwo();

          this.countTotalCoupons();

          //feedback
          this.countTotalUserFeedbacks();
          this.countAppUsers();
          this.countWebappUploaders();
        }
      }
    }, 3000);
  },

  methods: {
    //inventory
    countInventoryItems() {
      if (this.getUserPermission(this.user.permissions.inventory, "get")) {
        if (this.user.designation == "collaborator") {
          //
        } else if (this.user.designation == "webappUploader") {
          Api()
            .get("item", {
              params: {
                uploadedBy: this.user.uuid,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryCurrentItems = res.data;
              }
            });
        } else {
          Api()
            .get("item", {
              params: {
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryCurrentItems = res.data;
              }
            });
        }
      }
    },

    countInventoryOutOfStock() {
      if (this.getUserPermission(this.user.permissions.inventory, "get")) {
        if (this.user.designation == "collaborator") {
          //
        } else if (this.user.designation == "webAppUploader") {
          Api()
            .get("item", {
              params: {
                stock: 0,
                uploadedBy: this.user.uuid,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryOutOfStock = res.data;
              }
            });
        } else {
          Api()
            .get("item", {
              params: {
                stock: 0,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryOutOfStock = res.data;
              }
            });
        }
      }
    },

    countInventoryLowStock() {
      if (this.getUserPermission(this.user.permissions.inventory, "get")) {
        if (
          this.user.designation == "collaborator" ||
          this.user.designation == "webAppUploader"
        ) {
          //
        } else {
          Api()
            .get("item", {
              params: {
                stock: 3,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryLowStock = res.data;
              }
            });
        }
      }
    },

    countInventoryInactiveItems() {
      if (this.getUserPermission(this.user.permissions.inventory, "get")) {
        if (this.user.designation == "collaborator") {
          Api()
            .get("item", {
              params: {
                active: 0,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryInactiveItems = res.data;
              }
            });
        } else if (this.user.designation == "webAppUploader") {
          Api()
            .get("item", {
              params: {
                active: 0,
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.inventoryInactiveItems = res.data;
              }
            });
        } else {
          //ghanta
        }
      }
    },

    //order

    countTotalOrders() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalOrders = res.data;
            }
          });
      }
    },

    countOrderConfirmed() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 5,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderConfirmed = res.data;
            }
          });
      }
    },

    countOrderReturned() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 10,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderReturn = res.data;
            }
          });
      }
    },

    countOrderReturnApproved() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 9,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderReturnApprove = res.data;
            }
          });
      }
    },

    countOrderDelivered() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 7,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderDelivered = res.data;
            }
          });
      }
    },

    countOrderShipping() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 6,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderShipping = res.data;
            }
          });
      }
    },

    countOrderProcessing() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 4,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderProcessing = res.data;
            }
          });
      }
    },

    countOrderFailed() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 8,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderFailed = res.data;
            }
          });
      }
    },

    countOrderCancelled() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 1,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderCancelled = res.data;
            }
          });
      }
    },

    countOrderRequestedReturn() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order", {
            params: {
              status: 2,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.orderRequestedReturn = res.data;
            }
          });
      }
    },

    countTotalDeliveries() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalDeliveries = res.data;
            }
          });
      }
    },

    countUndeliverFailOne() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              status: 9,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.undeliverFailOne = res.data;
            }
          });
      }
    },

    countUndeliverFailTwo() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              status: 10,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.undeliverFailTwo = res.data;
            }
          });
      }
    },

    countDeliveryReady() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              status: 3,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.deliveryReady = res.data;
            }
          });
      }
    },

    countDeliveryOutForDelivery() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              status: 4,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.deliveryOutForDelivery = res.data;
            }
          });
      }
    },

    countDeliverySuccess() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              status: 5,
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.deliverySuccess = res.data;
            }
          });
      }
    },

    countTotalCoupons() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("delivery", {
            params: {
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalCoupons = res.data;
            }
          });
      }
    },

    countAppUsers() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        Api()
          .get("user", {
            params: {
              designation: "user",
              status: "active",
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.appUsers = res.data;
            }
          });
      }
    },

    countWebappUploaders() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        Api()
          .get("user", {
            params: {
              designation: "webappUploader",
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.webappUploaders = res.data;
            }
          });
      }
    },

    countTotalUserFeedbacks() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        Api()
          .get("user/feedback", {
            params: {
              count: "aajKuchTufaniKrteHai"
            }
          })
          .then(res => {
            if (res.data) {
              this.totalUserFeedbacks = res.data;
            }
          });
      }
    }
  }
};
</script>

<style>
#sidebarBoard {
  overflow-y: auto;
  height: 100vh;
  white-space: nowrap;
  padding-bottom: 100px;
}

.sidebar-icons {
  width: 28px;
  fill: #388e3c;
}

#usersidebar {
  z-index: 900;
  min-width: 260px;
  max-width: 260px;
  min-height: 100vh;
  position: fixed;
  transition: all 0.3s;
  background-color: #ffffff;
}

#usersidebar .header > span,
a[aria-expanded="true"] {
  font-size: 0.95em;
  color: #858585;
  font-weight: 800;
}

#usersidebar .header-active,
a[aria-expanded="true"] {
  background: rgba(56, 142, 60, 0.27);
}

#usersidebar .header-active > span,
a[aria-expanded="true"] {
  color: #388e3c;
  font-weight: 900;
}

#usersidebar ul li > .inner-a,
a[aria-expanded="true"] {
  font-size: 0.8em;
  color: #858585;
  font-weight: 700;
}

#usersidebar ul li > .active,
a[aria-expanded="true"] {
  color: #388e3c;
  background: rgba(56, 142, 60, 0.27);
}

@media (max-width: 768px) {
  /* #usersidebar {
    z-index: 999;
    left: -250px;
  } */
}

.caret-toggle[aria-expanded="true"]:after {
  transform: rotate(180deg);
}

/*for the animation*/
.caret-toggle:after {
  transition: 0.5s;
}
</style>



