<template>
  <div class="container-fluid" v-if="user">
    <Uploader v-if="user.designation=='webappUploader'" />
    <Collaborator v-else-if="user.designation=='collaborator'" />
    <Admin v-else />
  </div>
</template>
<script>
import Collaborator from "./Collaborator.vue";
import Admin from "./Admin.vue";
import Uploader from "./Uploader.vue";

export default {
  name: "MainDashboard",
  components: {
    Admin,
    Collaborator,
    Uploader
  }
};
</script>

<style src="@/assets/css/dashBoards.css" scoped></style>
<style src="@/assets/css/tableContent.css" scoped></style>



