export function getUserPermission(index, type) {
    let permissions = {
        1: {
            get: 1,
            post: 0,
            put: 0,
            delete: 0
        },
        2: {
            get: 0,
            post: 1,
            put: 0,
            delete: 0
        },
        3: {
            get: 0,
            post: 0,
            put: 1,
            delete: 0
        },
        4: {
            get: 0,
            post: 0,
            put: 0,
            delete: 1
        },
        5: {
            get: 1,
            post: 1,
            put: 0,
            delete: 0
        },
        6: {
            get: 1,
            post: 0,
            put: 1,
            delete: 0
        },
        7: {
            get: 1,
            post: 0,
            put: 0,
            delete: 1
        },
        8: {
            get: 0,
            post: 1,
            put: 1,
            delete: 0
        },
        9: {
            get: 0,
            post: 1,
            put: 0,
            delete: 1
        },
        10: {
            get: 0,
            post: 0,
            put: 1,
            delete: 1
        },
        11: {
            get: 1,
            post: 1,
            put: 1,
            delete: 0
        },
        12: {
            get: 1,
            post: 1,
            put: 0,
            delete: 1
        },
        13: {
            get: 0,
            post: 1,
            put: 1,
            delete: 1
        },
        14: {
            get: 1,
            post: 1,
            put: 0,
            delete: 1
        },
        15: {
            get: 1,
            post: 1,
            put: 1,
            delete: 1
        },
        16: {
            get: 0,
            post: 0,
            put: 0,
            delete: 0
        },
    };

    if (type == 'get') {
        if (permissions[index].get == 1) {
            return true;
        }
    } else if (type == 'post') {
        if (permissions[index].post == 1) {
            return true;
        }
    } else if (type == 'put') {
        if (permissions[index].put == 1) {
            return true;
        }
    } else if (type == 'delete') {
        if (permissions[index].delete == 1) {
            return true;
        }
    } else {
        return false;
    }

    return false;
};


