<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";
import Dashboard from "../Items/DashBoard.vue";
import Footer from "../Footer.vue";
import { bus } from "@/main";
import store from "@/store/store";
import { invLayoutMixin } from "@/components/Mixins/Inventory/invLayoutMixin.js";

export default {
  name: "VendorItems",
  mixins: [invLayoutMixin],
  data() {
    return {
      items: [],
      loading: false
    };
  },

  components: {
    Footer,
    Dashboard
  },

  computed: {
    size() {
      return store.state.inventoryPageSize;
    }
  },

  beforeRouteEnter(to, from, next) {
    // loading content
    Api()
      .get("item", {
        params: {
          vendor: to.params.vendorId,
          user: "aajaBhai",
          category: "fetch",
          page: 1,
          limit: 10,
          order: "name",
          orderWay: "asc"
        }
      })
      .then(res => {
        next(vm => {
          res.data.data.forEach(doc => {
            vm.items.push(item);
          });

          store.dispatch("patchinventoryPageCount", res.data.last_page);
          store.dispatch("patchinventoryPageNumber", res.data.current_page);
          store.dispatch("patchinventoryTotalItems", res.data.total);
          vm.loading = false;
        });
      });
  },

  created() {
    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  watch: {
    $route: "fetchItems"
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      if (!page) {
        page = 1;
      }

      Api()
        .get("item", {
          params: {
            vendor: this.$route.params.vendorId,
            user: "aajaBhai",
            category: "fetch",
            page: page,
            limit: this.size,
            order: this.order,
            orderWay: this.orderWay,
            uploadedBy: this.user.uuid
          }
        })
        .then(res => {
          res.data.data.forEach(doc => {
            {
              this.items.push(item);
            }
          });

          store.dispatch("patchinventoryPageCount", res.data.last_page);
          store.dispatch("patchinventoryPageNumber", res.data.current_page);
          store.dispatch("patchinventoryTotalItems", res.data.total);
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>



