<template>
  <div>
    <!-- header -->
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <div class="pl-4">
          <i class="far fa-list-alt"></i>
          <h5 class="pl-2 d-inline-block">
            <strong>
              <span v-if="status == 1">CANCELLED</span>
              <span v-if="status == 2">REQUESTED RETURN</span>
              <span v-if="status == 4">PENDING</span>
              <span v-if="status == 5">CONFIRMED</span>
              <span v-if="status == 5">DELIVERED</span>
              <span v-if="status == 8">FAILED</span>
              <span v-if="status == 9">RETURNED APPROVED</span>
              <span v-if="status == 10">RETURNED</span>
              ORDERS
            </strong>
          </h5>
        </div>
      </div>
      <div class="col-6">
        <!-- order select not for return, cancelled orders -->
        <form
          class="form-inline float-right"
          v-if="
            status &&
              (status == 1 ||
                status == 3 ||
                status == 4 ||
                status == 5 ||
                status == 7)
          "
        >
          <span class="font-weight-semibold pr-3">Select Order Status</span>
          <select
            v-model="action"
            name="status"
            class="custom-select custom-select-sm"
            @change="updateAction()"
          >
            <option disabled="disabled" selected="selected">Choose Action</option>
            <!-- <option value="1">Cancel</option> -->
            <option value="1" v-if="status == 3 || status == 4 || status == 5 || status == 6">Cancel</option>
            <option value="2" v-if="status == 7">Request Return</option>
            <option value="3" v-if="status == 1">Place</option>
            <option value="4" v-if="status == 3">Processing</option>
            <option value="5" v-if="status == 4">Packed</option>
            <option value="d5" v-if="status == 5">Ready For Pickup</option>
            <!-- <option value="6" v-if="status==5">Out For Delivery</option> -->
            <!-- <option value="7" v-if="status==6">Delivered</option> -->
            <option value="9" v-if="status == 2">Return Approved</option>
            <!-- <option value="10" v-if="status==9">Returned</option> -->
            <option value="8">Failed</option>
            <option value="delete">Delete</option>
            <!-- <option
                      :value="status.id"
                      v-for="status in orderStatus"
                      :key="status.id"
            >{{status.status}}</option>-->
          </select>
        </form>
      </div>
    </div>
    <hr class="pl-4 pr-4" />
    <div class="table-responsive table-scroll mt-4">
      <table class="table table-hover table-sm table-borderless table-content text-left">
        <thead>
          <tr>
            <th
              v-if="
                status &&
                  (status == 1 ||
                    status == 3 ||
                    status == 4 ||
                    status == 5 ||
                    status == 7)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck"
                    @click="selectAll"
                    v-model="select"
                  />
                  <label class="custom-control-label" for="customCheck"></label>
                </div>
              </form>
            </th>
            <th>Order Id</th>
            <th>Order By</th>
            <th>Products</th>
            <th>Total Items</th>
            <th>Order Items</th>
            <th>Payment Mode</th>
            <th>Payment Status</th>
            <th>Date</th>
            <th>Amount</th>
            <th>Order Status</th>
            <th
              v-if="
                status == 3 ||
                  status == 4 ||
                  status == 5 ||
                  status == 6 ||
                  status == 7
              "
            >Expected Delivery By</th>
            <th v-if="status == 1 || status == 2 || status == 10 || status == 9">Reason</th>
            <th v-if="status == 1 || status == 10">Transactions</th>
            <th>Placed From</th>
            <th></th>
          </tr>
        </thead>
        <tbody v-if="orders.length > 0">
          <tr v-for="(order, index) in orders" :key="order.id">
            <td
              v-if="
                status &&
                  (status == 1 ||
                    status == 3 ||
                    status == 4 ||
                    status == 5 ||
                    status == 7)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    :id="order.orderId"
                    v-model="order.checked"
                    :value="order.orderId"
                  />
                  <label class="custom-control-label" :for="order.orderId"></label>
                </div>
              </form>
            </td>
            <td>
              {{ order.orderId.substring(0, 4) + "..." }}
              <i
                class="far fa-copy pl-1"
                style="cursor:pointer"
                @click="copyProductId(order.orderId)"
              ></i>
            </td>
            <td>
              {{ order.deliveryAddress.name }}
              <br />
              <span v-if="order.user.mobileNumber">{{ order.user.mobileNumber }}</span>
              <span v-else>N/A</span>
              <br />
              <small>
                <b>Registered By:</b>
                <span v-if="order.user.mobileNumber">{{ order.user.mobileNumber }}</span>
                <span v-if="order.user.email">{{ order.user.email }}</span>
              </small>
            </td>
            <td>
              <div v-if="order.products.length > 2">
                <span v-for="product in order.products.slice(0, 2)" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" /> +
                </span>
                <span>
                  <b>{{ order.products.length - 2 }} more</b>
                </span>
              </div>
              <div v-else>
                <span v-for="product in order.products" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" />
                </span>
              </div>
            </td>
            <td>{{ order.orderedItemsCount }}</td>
            <td>{{ order.availableItems }}</td>
            <td>
              <span class="text-dark">{{ order.paymentMode.mode }}</span>
            </td>
            <td>
              <span class="bagde badge-waiting pl-1 pr-1" v-if="order.paidAmount == 0">Not paid</span>
              <span
                class="bagde badge-paid pl-1 pr-1"
                v-else-if="order.paidAmount == order.payment"
              >Paid</span>

              <span class="bagde badge-failed pl-1 pr-1" v-else>Pending</span>
            </td>
            <td>
              <span class="text-dark">{{ order.orderDate }}</span>
              <br />
              <small>{{ order.orderTime }}</small>
            </td>
            <td class="text-dark">₹{{ order.payment }}</td>
            <td>
              <span
                class="badge badge-cancelled pl-2 pr-2"
                v-if="order.orderStatus.id == 1"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id == 2"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-processing pl-2 pr-2"
                v-else-if="order.orderStatus.id == 4"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-packed pl-2 pr-2"
                v-else-if="order.orderStatus.id == 5"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-shipping pl-2 pr-2"
                v-else-if="order.orderStatus.id == 6"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-delivered pl-2 pr-2"
                v-else-if="order.orderStatus.id == 7"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-failed pl-2 pr-2"
                v-else-if="order.orderStatus.id == 8"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id == 9"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-return pl-2 pr-2"
                v-else-if="order.orderStatus.id == 10"
              >{{ order.orderStatus.status }}</span>
            </td>
            <td
              v-if="
                status == 3 ||
                  status == 4 ||
                  status == 5 ||
                  status == 6 ||
                  status == 7
              "
            >{{ order.estDeliveryTime[1] }}</td>
            <td v-if="order.orderBack">{{ order.orderBack.reason }}</td>
            <td v-if="status == 1 || status == 10">
              <div v-if="order.transactions.length > 0">
                <span v-for="transaction in order.transactions" :key="transaction.transactionId">
                  {{ transaction.transactionId }} - ₹{{
                  transaction.amount
                  }}
                </span>
              </div>
              <span v-else>No transaction</span>
            </td>
            <td>
              <span v-if="order.placedFrom == 1">App</span>
              <span v-if="order.placedFrom == 2">WhatsApp</span>
              <span v-if="order.placedFrom == 3">Call</span>
              <span v-if="order.placedFrom == 4">SMS</span>

              <!-- address -->
              <br />
              <span>
                {{ order.deliveryAddress.houseOfficeNo }},
                {{ order.deliveryAddress.street }},
                {{ order.deliveryAddress.city }},
                {{ order.deliveryAddress.landmark }},
                {{ order.deliveryAddress.pincode }}
              </span>
            </td>

            <td class="text-dark text-center align-items-center">
              <div class="dropdown dropleft">
                <a
                  href="#"
                  class="float-right"
                  role="button"
                  :id="'dropdownMenuLink'.index"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fa fa-lg fa-ellipsis-v" aria-hidden="true"></i>
                </a>
                <div class="dropdown-menu" :aria-labelledby="'dropdownMenuLink'.index">
                  <router-link
                    :to="{
                      name: 'OrderReturnSlip',
                      params: { slipType: 'returnSlip', orderId: order.orderId }
                    }"
                    tag="a"
                    class="pr-1 dropdown-item"
                    target="_blank"
                    v-if="status == 2 || status == 9"
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Return Slip</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'OrderBill',
                      params: { slipType: 'bill', orderId: order.orderId }
                    }"
                    tag="a"
                    class="pr-1 dropdown-item"
                    target="_blank"
                    v-else
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Generate Bill</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'ViewOrder',
                      params: { orderId: order.orderId }
                    }"
                    target="_blank"
                    class="pr-1 dropdown-item"
                  >
                    <i class="fa fa-eye pr-2"></i>
                    <span class="pl-2 text-center">View</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'OrderEdit',
                      params: { orderId: order.orderId }
                    }"
                    target="_blank"
                    class="pr-1 dropdown-item"
                    v-if="status == 3 || status == 4 || status == 5"
                  >
                    <i class="fa fa-pencil-alt pr-2"></i>
                    <span class="pl-2 text-center">Edit</span>
                  </router-link>
                  <a
                    :href="
                      'https://www.google.com/maps/search/' +
                        order.ordererLocation
                    "
                    class="dropdown-item"
                    target="_blank"
                  >
                    <i class="fa fa-map-pin pr-2" aria-hidden="true"></i>
                    <span class="pl-2 text-center">Locate</span>
                  </a>
                  <div v-if="status == 1 || status == 10" class="dropdown-item">
                    <br />
                    <span v-if="order.refundId">Refunded - ₹{{ order.refund.amount }}</span>
                    <span v-else-if="order.paidAmount == 0">Refund Not Possible</span>
                    <button
                      v-else
                      data-toggle="modal"
                      data-target="#refundModal"
                      @click="refund(index)"
                    >Make refund</button>
                  </div>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="9" class="text-center">
              <div>
                <img
                  class="card-img-top img-fluid"
                  src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                  alt="Card image cap"
                  style="width:20em"
                />
                <div class="card-body">
                  <p class="card-text">Oops! There ain't any orders here</p>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!--refund modal -->
    <div
      class="modal fade"
      id="refundModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="refundModalLabel"
      aria-hidden="true"
      v-if="refundOrder"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Refund</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <span>Refunding to {{ refundOrder.user.name }}</span>
            <form class="form">
              <!-- payment mode-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Payment Mode</label>
                  {{ refundPaymentMode }}
                  <select
                    v-model="refundPaymentMode"
                    class="form-control form-control-sm"
                    :disabled="disable"
                  >
                    <option
                      :value="paymentMode.id"
                      class="font-weight-bold"
                      v-for="paymentMode in paymentModes"
                      :key="paymentMode.id"
                    >{{ paymentMode.mode }}</option>
                  </select>
                </div>
              </div>
              <!-- amount -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Amount</label>
                  <input
                    type="number"
                    v-model.number="refundAmount"
                    class="form-control form-control-sm"
                    required
                    :max="refundOrder.paidAmount"
                    min="0"
                    :disabled="disable"
                  />
                  <small>Max Amount: {{ refundOrder.paidAmount }}</small>
                </div>
              </div>
              <!-- transaction Id-->
              <div class="form-row" v-if="refundPaymentMode != 1 && refundPaymentMode != 7">
                <div class="col-sm-12">
                  <label>Transaction Id</label>
                  <input
                    type="text"
                    v-model="refundTransactionId"
                    class="form-control form-control-sm"
                    required
                    :disabled="disable"
                  />
                  <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                </div>
              </div>
              <!-- transaction date/time-->
              <div class="form-row" v-if="refundPaymentMode != 1 && refundPaymentMode != 7">
                <div class="col-sm-12">
                  <datetime v-model="refundTransactionTime" type="datetime">
                    <label for="expiriesAt" slot="before">Transaction Time</label>
                    <small class="description" slot="before">(When transaction is made)</small>
                  </datetime>
                  <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="refundingOrder()">Refund</button>
          </div>
        </div>
      </div>
    </div>
    <!-- refund modal ends -->

    <OrderFooter />
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>

<script>
import Vue from "vue";
import { bus } from "@/main";
import OrderFooter from "./OrderFooter.vue";
import { Datetime } from "vue-datetime";
import Api from "@/services/Api.js";

export default {
  name: "DashBoard",
  components: {
    OrderFooter,
    datetime: Datetime
  },
  props: {
    status: Number,
    newOrders: Array,
    disable: Boolean
  },

  data() {
    return {
      orderStatus: [],
      search: null,

      select: false,
      action: null,

      message: null,
      success: null,
      loading: this.disable,

      paymentModes: [],
      refundTransactionId: null,
      refundAmount: 0,
      refundPaymentMode: 7,
      refundTransactionTime: null,
      refundOrder: null,
      refundIndex: 0
    };
  },

  computed: {
    orders() {
      return this.newOrders;
    }
  },

  created() {
    this.getOrderStatus();

    bus.$on("paginateOrder", data => {
      this.paginateOrder(data["page"]);
    });
  },

  methods: {
    showModal() {
      this.isRefundModalVisible = true;
    },
    closeModal() {
      this.isRefundModalVisible = false;
    },

    copyProductId(id) {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        let prodID = "#" + id;
        let idToCopy = document.querySelector(prodID);
        idToCopy.setAttribute("type", "text");
        idToCopy.select();

        try {
          let successful = document.execCommand("copy");
          let message = successful ? "Copied!" : "Unable to Copy!";
          alert(message);
        } catch (err) {
          alert("Whoops! unable to copy");
        }

        idToCopy.setAttribute("type", "hidden");
        window.getSelection().removeAllRanges();
      } else {
        alert("You aren't allowed to do this");
      }
    },

    updateAction() {
      if (
        this.getUserPermission(this.user.permissions.salesOrder, "put") ||
        this.getUserPermission(this.user.permissions.salesOrder, "delete")
      ) {
        this.orders.forEach((order, index) => {
          if (order.checked) {
            if (
              this.action == "delete" &&
              this.getUserPermission(this.user.permissions.salesOrder, "delete")
            ) {
              if (confirm("You are about to delete " + order.orderId)) {
                this.loading = true;
                let url = "order/" + order.orderId;
                Api()
                  .post(url, {
                    _method: "DELETE"
                  })
                  .then(res => {
                    if (res.data) {
                      this.message = "Order successfully deleted";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      this.orders.splice(index, 1);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    }
                  })
                  .catch(err => {
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            } else if (
              this.getUserPermission(this.user.permissions.salesOrder, "put")
            ) {
              if (this.action == "d5") {
                var action = 3;
                this.loading = true;
                Api()
                  .get("delivery", {
                    params: {
                      orderId: order.orderId
                    }
                  })
                  .then(res => {
                    let url = "delivery/" + res.data[0].id;
                    Api()
                      .post(url, {
                        _method: "PUT",
                        status: action
                      })
                      .then(res => {
                        this.message = "Successfully Updated and moved";
                        this.success = true;
                        this.loading = false;
                        this.action = null;

                        //remove that order after save
                        if (res.data == "Success") {
                          this.orders.splice(index, 1);
                        }

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      })
                      .catch(err => {
                        this.message = "Some error occured";
                        this.success = true;
                        this.loading = false;
                        this.action = null;

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      });
                  });
              } else if (this.action == "1" || this.action == "2") {
                this.loading = true;
                Api()
                  .post("order/" + order.orderId, {
                    _method: "PUT",
                    reason: "Done from webapp",
                    preferredPaymentMode: 1,
                    status: this.action
                  })
                  .then(res => {
                    this.message = "Successfully Updated and moved";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    //remove that order after save
                    if (res.data == "Success") {
                      this.orders.splice(index, 1);
                    }

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  })
                  .catch(err => {
                    this.message = "Some error occured";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              } else {
                this.loading = true;
                Api()
                  .post("order/" + order.orderId, {
                    _method: "PUT",
                    status: this.action
                  })
                  .then(res => {
                    this.message = "Successfully Updated and moved";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    if (res.data == "Success") {
                      this.orders.splice(index, 1);
                    }

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  })
                  .catch(err => {
                    this.message = "Some error occured";
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            }
          }
        });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    getOrderStatus() {
      if (this.getUserPermission(this.user.permissions.salesOrder, "get")) {
        Api()
          .get("order/status")
          .then(res => {
            this.orderStatus = res.data;
          })
          .catch(err => {
            console.log(err);
          });
      }
    },

    paginateOrder(page) {
      this.$emit("fetchOrders", page);
    },

    refund(index) {
      this.refundIndex = index;
      this.refundOrder = this.orders[index];
      if (this.refundOrder.orderBack) {
        this.refundPaymentMode = this.refundOrder.orderBack.preferredPaymentMode;
      } else {
        this.refundPaymentMode = 7;
      }
      this.paymentModes = [];
      Api()
        .get("payment/mode")
        .then(res => {
          res.data.forEach(doc => {
            this.paymentModes.push(doc);
          });
        })
        .catch(err => {
          console.log("Payment Modes can't be fetched");
        });
    },

    refundingOrder() {
      if (
        this.getUserPermission(this.user.permissions.salesOrder, "put") &&
        this.refundAmount > 0
      ) {
        this.loading = true;
        Api()
          .post("order/" + this.refundOrder.orderId, {
            _method: "PUT",
            refund: "yes",
            refundAmount: this.refundAmount,
            refundPaymentMode: this.refundPaymentMode,
            refundTransactionId: this.refundTransactionId,
            refundTransactionTime: this.refundTransactionTime
          })
          .then(res => {
            if (res.data == "Success") {
              this.message = "Success";
              this.refundOrder = null;
              this.order[this.refundIndex].refund = {
                amount: this.refundAmount
              };
            } else {
              this.message = "Some Error Occured";
            }
            this.success = true;
            this.loading = false;

            var status = {};

            //colsing inputs
            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            console.log(err.message);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      } else {
        alert("You aren't allowed to do this");
      }
    },

    selectAll() {
      this.orders.forEach(order => {
        if (!this.select) {
          order.checked = true;
          //Vue.set(order, checked, true);
        } else {
          order.checked = false;
        }
      });
    }
  }
};
</script>



