<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>
          Uploader :
          <span class="response">{{ uploader.name }}</span>
        </h4>
      </div>
      <div class="col-6 content-header text-center small">
        <span>doorstep > uploaders > {{ uploader.name }}</span>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <!-- uploader -->
        <div class="card content border-1 shadow-sm mb-4">
          <div class="card-body container-fluid">
            <div class="row order-content">
              <div class="col-4 order-by-header">
                <!-- order guy -->
                <div class="media pt-2">
                  <div class="media-body pl-4">
                    <h6>{{ uploader.name }}</h6>
                  </div>
                </div>
              </div>
              <div class="col-4" style="font-size:small;">
                <div>
                  <span class="font-weight-bold">Total Uploads</span>
                  <span class="response">{{ uploader.totalUploads }}</span>
                </div>
                <div>
                  <span class="font-weight-bold">Amount Paid</span>
                  <span class="response">{{ uploader.amountPaid }}</span>
                </div>
                <div>
                  <span class="font-weight-bold">Total Amount</span>
                  <span class="response">{{ uploader.totalPay }}</span>
                </div>
              </div>
              <div class="col-4">
                <button
                  class="btn btn-sm btn-go-green font-weight-bold d-inline pl-3 pr-3"
                  data-toggle="collapse"
                  data-target="#addTransaction"
                >Add Transaction</button>
              </div>
            </div>
          </div>
        </div>
        <!-- transaction -->
        <div class="card content border-1 shadow-sm mb-4 collapse" id="addTransaction">
          <div class="card-body container-fluid">
            <div class="row order-content">
              <div class="col-12 order-by-header">
                <h6>Add Transaction</h6>

                <form>
                  <div class="form-row">
                    <div class="col-sm-6">
                      <input
                        type="text"
                        class="form-control"
                        v-model="transactionId"
                        placeholder="Transaction Id"
                        aria-label="Transaction Id"
                        aria-describedby="basic-addon1"
                      />
                    </div>
                    <div class="col-sm-6"></div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-6">
                      <input
                        type="number"
                        class="form-control"
                        v-model.number="amount"
                        placeholder="Amount"
                        aria-label="Amount"
                        aria-describedby="basic-addon1"
                        min="0"
                      />
                      <span
                        class="text-danger"
                        v-if="amount > uploader.totalPay"
                      >Beware! You're paying more</span>
                    </div>
                    <div class="col-sm-6">
                      <select class="form-control" v-model="paymentMode">
                        <option value="4">Google Pay</option>
                      </select>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-6">
                      <input
                        type="date"
                        class="form-control"
                        v-model="date"
                        placeholder="Date"
                        aria-label="Date"
                        aria-describedby="basic-addon1"
                      />
                    </div>
                    <div class="col-sm-6">
                      <input
                        type="text"
                        class="form-control"
                        v-model="time"
                        placeholder="Time"
                        aria-label="Time"
                        aria-describedby="basic-addon1"
                      />
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-12 text-center">
                      <button
                        class="btn btn-sm btn-go-green font-weight-bold d-inline pl-3 pr-3"
                        @click.prevent="addTransaction"
                      >Add</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <!-- -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="row pt-2">
              <div class="col-6">
                <form class="form-inline">
                  <div class="input-group content-search border">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-search"></i>
                      </span>
                    </div>
                    <input
                      type="text"
                      v-model="search"
                      class="form-control hz-input"
                      placeholder="Search Here..."
                      aria-label="Search Here..."
                      aria-describedby="basic-addon1"
                      @keyup="searching"
                    />
                  </div>
                </form>
              </div>
              <div class="col-6"></div>
            </div>
            <div class="table-responsive pt-4">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>Sr.No</th>
                    <th>Transaction Id</th>
                    <th>Payment Mode</th>
                    <th>Amount</th>
                    <th>Date</th>
                    <th>Time</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody v-if="uploader.transactions">
                  <tr v-for="(transaction, index) in uploader.transactions" :key="transaction.id">
                    <td>{{ index + 1 }}</td>
                    <td>{{ transaction.transactionId }}</td>
                    <td>{{ transaction.paymentMode }}</td>
                    <td>₹{{ transaction.amount }}</td>
                    <td>{{ transaction.date }}</td>
                    <td>{{ transaction.time }}</td>
                    <td class="text-dark">
                      <span>
                        <a
                          class="fa fa-trash fa-md"
                          style="color:#424242;"
                          href="#"
                          data-toggle="modal"
                          data-target="#deleteItem"
                          data-placement="bottom"
                          title="delete this item"
                          @click="deleteTransaction(index)"
                        ></a>
                      </span>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr>
                    <td colspan="7">No transaction made yet!</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//import orderView from "./SingleView.vue";
import Api from "@/services/Api.js";

export default {
  name: "UploaderTransaction",

  data() {
    return {
      uploader: [],

      //nya transaction
      transactionId: null,
      amount: 0,
      date: null,
      time: null,
      paymentMode: null,

      search: null,
      searchArray: []
    };
  },

  beforeRouteEnter(to, from, next) {
    // loading content
    Api()
      .get("user", {
        params: {
          uuid: to.params.uploadId,
          designation: "webappUploader",
          package: "laiye",
          transactions: "laiye"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            {
              let totalUploads = 0;
              let totalPay = 0;
              let amountPaid = 0;
              let amountDue = 0;

              // package ka detail nikal liya
              doc.package.forEach(element => {
                totalUploads = totalUploads + Number(element.totalUploads);
                if (element.package == 3) {
                  totalPay = totalPay + 3 * Number(element.totalUploads);
                } else {
                  totalPay = totalPay + Number(element.totalUploads);
                }
              });

              //trnasaction ka nikalo
              doc.upload_transactions.forEach(element => {
                amountPaid = amountPaid + Number(element.amount);
              });

              amountDue = totalPay - amountPaid;

              let details = {
                uploaderId: doc.uuid,
                name: doc.name,
                email: doc.email,
                totalUploads: totalUploads,
                amountPaid: amountPaid,
                totalPay: totalPay,
                amountDue: amountDue,
                transactions: doc.upload_transactions,

                checked: false
              };

              vm.uploader = details;
            }
          });
        });
      })
      .catch(err => {
        console.log(err);
      });
  },

  watch: {
    $route: "fetchUploader"
  },

  methods: {
    addTransaction() {
      if (
        this.getUserPermission(this.user.permissions.customer, "post") &&
        this.amount <= this.uploader.totalPay
      ) {
        Api()
          .post("user/uploader/transaction", {
            transactionId: this.transactionId,
            amount: this.amount,
            paymentMode: this.paymentMode,
            date: this.date,
            to: this.uploader.uploaderId,
            time: this.time
          })
          .then(res => {
            if (res.data) {
              this.uploader.transactions.unshift(res.data);

              //
              let amountDue = Number(amountDue) - this.amount;
              let amountPaid = Number(amountPaid) + this.amount;
              Vue.set(this.uploader, "amountDue", amountDue);
              Vue.set(this.uploader, "amountPaid", amountPaid);

              // now
            }
            //// to show
            // this.message = "Upload Success";
            // this.success = true;
            // this.disable = false;

            // setTimeout(() => {
            //   this.success = false;
            //   this.message = null;
            // }, 2000);
          });
      } else {
        console.log("out");
        if (this.amount > this.uploader.totalPay) {
          alert("Kushi ko control kre aur zada paisa na de");
        }
      }
    },

    deleteTransaction(index) {
      if (this.getUserPermission(this.user.permissions.customer, "delete")) {
        Api()
          .post(
            "user/uploader/transaction/" +
              this.uploader.transactions[index].transactionId,
            {
              _method: "DELETE"
            }
          )
          .then(res => {
            if (res.data == "done") {
              let amountDue =
                Number(amountDue) +
                Number(this.uploader.transactions[index].amount);
              let amountPaid =
                Number(amountPaid) -
                Number(this.uploader.transactions[index].amount);
              Vue.set(this.uploader, "amountDue", amountDue);
              Vue.set(this.uploader, "amountPaid", amountPaid);

              this.uploader.transactions.splice(index, 1);
            }
            //// to show
            // this.message = "Upload Success";
            // this.success = true;
            // this.disable = false;

            // setTimeout(() => {
            //   this.success = false;
            //   this.message = null;
            // }, 2000);
          });
      }
    },
    fetchUploader() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        Api()
          .get("user", {
            params: {
              uuid: this.$route.params.uploadId,
              designation: "webappUploader",
              transactions: "laiye"
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let totalUploads = 0;
              let totalPay = 0;
              let amountPaid = 0;
              let amountDue = 0;

              // package ka detail nikal liya
              doc.package.forEach(element => {
                totalUploads = totalUploads + Number(element.totalUploads);
                if (element.package == 3) {
                  totalPay = totalPay + 3 * Number(element.totalUploads);
                } else {
                  totalPay = totalPay + Number(element.totalUploads);
                }
              });

              //trnasaction ka nikalo
              doc.upload_transactions.forEach(element => {
                amountPaid = amountPaid + Number(element.amount);
              });

              amountDue = totalPay - amountPaid;

              let details = {
                uploaderId: doc.uuid,
                name: doc.name,
                email: doc.email,
                totalUploads: totalUploads,
                amountPaid: amountPaid,
                totalPay: totalPay,
                amountDue: amountDue,
                transactions: doc.upload_transactions,

                checked: false
              };

              this.uploader = details;
            });
          })
          .catch(err => {
            console.log(err);
          });
      }
    },

    searching() {
      if (this.search) {
        this.transactions = this.searchArray.filter(
          v => v.transactionId.toLowerCase().indexOf(this.search.trim()) != -1
        );
      } else {
        this.transactions = this.searchArray;
      }
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>

<style scoped>
.order-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.order-by-header .heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.order-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.order-content {
  font-size: small;
}

.order-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 700;
}
</style>



