<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>UPLOADER's DETAILS</h4>
      </div>
      <div class="col-6 content-header small">
        <span>doorstep > uploaders</span>
      </div>

      <div class="row">
        <div class="col-12 mt-4">
          <!-- <DeliveryView/> -->
          <div class="card mt-4 content border-1 shadow-sm">
            <div class="card-body">
              <div class="row pt-2 mb-4">
                <div class="col-6 mb-4">
                  <form class="form-inline">
                    <div class="input-group content-search border">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">
                          <i class="fa fa-search"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        v-model="search"
                        class="form-control hz-input"
                        placeholder="Search Here..."
                        aria-label="Search Here..."
                        aria-describedby="basic-addon1"
                        @keyup="searching"
                      />
                    </div>
                  </form>
                </div>
                <div class="col-6">
                  <!-- select -->
                  <form class="form-inline float-right">
                    <span class="font-weight-semibold pr-3">Select Order Status</span>
                    <select
                      v-model="action"
                      name="status"
                      class="custom-select"
                      @change="updateAction"
                    >
                      <option selected>Choose Action</option>
                      <option value="paid">Paid</option>
                    </select>
                  </form>
                </div>
              </div>
              <div class="row">
                <div class="table-responsive col-12">
                  <table
                    class="table table-hover table-sm table-borderless table-content text-left"
                  >
                    <thead>
                      <tr>
                        <th>
                          <form>
                            <div class="custom-control custom-checkbox">
                              <input
                                type="checkbox"
                                class="custom-control-input"
                                id="customCheck"
                                @click="selectAll"
                                v-model="select"
                              />
                              <label class="custom-control-label" for="customCheck"></label>
                            </div>
                          </form>
                        </th>
                        <th>Uploader Id</th>
                        <th>Name</th>
                        <th>Package</th>
                        <th>Uploads(per package)</th>
                        <th>Total Uploads</th>
                        <th>Total Kamai</th>
                        <th>Amount Paid</th>
                        <th>Amount Due</th>
                        <th>Transactions</th>
                        <th>Payment Status</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(uploader, index) in uploaders" :key="uploader.id">
                        <td>
                          <form>
                            <div class="custom-control custom-checkbox">
                              <input
                                type="checkbox"
                                class="custom-control-input"
                                :id="uploader.uploaderId"
                                v-model="uploader.checked"
                                :value="uploader.uploaderId"
                              />
                              <label class="custom-control-label" :for="uploader.uploaderId"></label>
                            </div>
                          </form>
                        </td>
                        <td>#{{ uploader.id }}</td>
                        <td>{{ uploader.name }}</td>
                        <td>
                          <tr v-for="pack in uploader.package" :key="pack.id">
                            <td>₹{{ pack.package }}</td>
                          </tr>
                        </td>

                        <td>
                          <tr v-for="pack in uploader.package" :key="pack.id">
                            <td>{{ pack.totalUploads }}</td>
                          </tr>
                        </td>

                        <td>{{ uploader.totalUploads }}</td>

                        <td>₹{{ uploader.totalPay }}</td>
                        <td class="font-weight-bold">₹{{ uploader.amountPaid }}</td>
                        <td>₹{{ uploader.amountDue }}</td>
                        <td class="font-weight-bold text-center">
                          <router-link
                            :to="{
                              name: 'UploaderTransaction',
                              params: { uploadId: uploader.id }
                            }"
                            tag="a"
                            target="_blank"
                          >{{ uploader.transactions }}</router-link>
                        </td>
                        <td>
                          <span
                            class="bagde badge-paid pl-1 pr-1"
                            v-if="
                              uploader.totalPay == 0 &&
                                uploader.amountPaid < uploader.totalPay
                            "
                          >Ghanta</span>
                          <span
                            class="bagde badge-paid pl-1 pr-1"
                            v-else-if="uploader.amountPaid == uploader.totalPay"
                          >Paid</span>
                          <span
                            class="bagde badge-waiting pl-1 pr-1"
                            v-else-if="
                              uploader.totalPay != uploader.amountPaid &&
                                uploader.amountDue > 0
                            "
                          >Pending</span>
                          <span
                            class="bagde badge-waiting pl-1 pr-1"
                            v-else-if="uploader.amountPaid > uploader.totalPay"
                          >Zada ho rha</span>
                          <span
                            class="bagde badge-failed pl-1 pr-1"
                            v-else-if="uploader.amountPaid == 0"
                          >Not Paid</span>
                        </td>
                        <td>
                          <tr v-for="(pack, packIndex) in uploader.package" :key="pack.id">
                            <td>
                              <i
                                class="fa fa-trash pr-2"
                                aria-hidden="true"
                                @click="deleteUploader(index, packIndex)"
                              ></i>
                              <i
                                class="fa fa-ban pr-2"
                                aria-hidden="true"
                                @click="banUploader(index, packIndex, 'banned')"
                                v-if="pack.status == 'active'"
                              ></i>
                              <i
                                class="fa fa-street-view pr-2"
                                aria-hidden="true"
                                @click="banUploader(index, packIndex, 'active')"
                                v-if="pack.status == 'banned'"
                              ></i>
                              <i
                                class="fa fa-user-times pr-2"
                                aria-hidden="true"
                                v-if="pack.status == 'packageChanged'"
                              ></i>
                            </td>
                          </tr>
                        </td>

                        <td>
                          <router-link
                            :to="{
                              name: 'UploaderItems',
                              params: { uploaderId: uploader.id }
                            }"
                            tag="a"
                            target="_blank"
                          >
                            <i class="fas fa-eye"></i>
                          </router-link>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import Api from "@/services/Api.js";

export default {
  name: "Uploaders",

  data() {
    return {
      uploaders: [],
      editUploader: [],
      loading: false,

      //search
      search: null,
      searchArray: [],

      select: false,
      action: null
    };
  },

  mounted() {
    this.fetchuploaders();
  },

  methods: {
    banUploader(index, packIndex, status) {
      if (this.getUserPermission(this.user.permissions.customer, "put")) {
        this.loading = true;

        Api()
          .post(
            "user/uploader/" + this.uploaders[index].package[packIndex].id,
            {
              _method: "put",
              status: status
            }
          )
          .then(res => {
            // updating data
            Vue.set(
              this.uploaders[index].package[packIndex],
              "status",
              res.data.status
            );
            this.loading = false;
          })
          .catch(err => {
            console.log(err.message);
          });
      }
    },

    fetchuploaders() {
      if (this.getUserPermission(this.user.permissions.customer, "get")) {
        this.loading = true;
        Api()
          .get("user", {
            params: {
              designation: "webappUploader",
              package: "kuchLaiye",
              transactions: "kuchLaiye"
            }
          })
          .then(res => {
            let uploadersList = [];
            res.data.forEach(doc => {
              let totalUploads = 0;
              let totalPay = 0;
              let amountPaid = 0;
              let amountDue = 0;

              // package ka detail nikal liya
              doc.package.forEach(element => {
                totalUploads = totalUploads + Number(element.totalUploads);
                if (element.package == 3) {
                  totalPay = totalPay + 3 * Number(element.totalUploads);
                } else {
                  totalPay = totalPay + Number(element.totalUploads);
                }
              });

              //trnasaction ka nikalo
              doc.upload_transactions.forEach(element => {
                amountPaid = amountPaid + Number(element.amount);
              });

              amountDue = totalPay - amountPaid;

              let uploader = {
                id: doc.uuid,
                uploaderId: doc.userId,
                name: doc.name,
                email: doc.email,
                package: doc.package,
                totalPay: totalPay,
                totalUploads: totalUploads,
                transactions: doc.upload_transactions.length,
                amountPaid: amountPaid,
                amountDue: amountDue,

                checked: false
              };

              uploadersList.push(uploader);
            });

            this.uploaders = uploadersList;

            //array for search
            this.searchArray = this.uploaders;

            // for edit creating array
            var Anarray = [];
            for (var i = 0; i < this.uploaders.length; i++) {
              Anarray.push({
                //paymentMode: false,
                amountPaid: false
              });
            }
            this.editUploader = Anarray;
          });

        this.loading = false;
      }
    },

    deleteUploader(index, packIndex) {
      if (this.getUserPermission(this.user.permissions.customer, "delete")) {
        //only admin can delete
        if (
          confirm(
            "You are about to delete " +
              this.uploaders[index].name +
              " from the records"
          )
        ) {
          this.loading = true;

          Api()
            .post(
              "user/uploader/" + this.uploaders[index].package[packIndex].id,
              {
                _method: "delete",
                uploaderId: this.uploaders[index].package[packIndex].uploaderId
              }
            )
            .then(res => {
              if (res.data == "done") {
                this.uploaders[index].package.splice(packIndex, 1);
              }
            })
            .catch(err => {
              console.log(err.message);
            });

          this.loading = false;
        }
      }
    },

    searching() {
      if (this.search) {
        this.uploaders = this.searchArray.filter(
          v => v.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1
        );
      } else {
        this.uploaders = this.searchArray;
      }
    },

    selectAll() {
      this.uploaders.forEach(uploader => {
        console.log(this.select);
        if (!this.select) {
          uploader.checked = true;
          //Vue.set(uploader, checked, true);
        } else {
          uploader.checked = false;
        }
      });
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



